.main-panel[data-ui-category="my"] {
	.info-wrap {margin:20px 0 50px;
		strong {display:block; margin-bottom:3px; font-size:$fz-sm; font-weight:$fw-b;}
		.list-star {
			&:first-of-type {margin-bottom:10px;}
			li {
				&:first-child {margin-top:0;}
			}
		}
		& + .list-wrap {
			@include mediaQuery(pc_under) {
				.list-head {margin-bottom:15px; border-bottom:2px solid #999;}
			}
		}
	}
}

//반응형 테이블
.tb-list-wrap{margin:20px 0 5px; border-top:2px solid #999;
	> li{border-bottom:1px solid #e4e4e4;}
	.tb-con-res{width:100%;
		li{float:left;
		  dl{border:0;
			dt{text-align:center;padding:16px 0 15px;border-bottom:1px solid #e4e4e4;background:#f6f6f6;}
			dd{text-align:center;padding:10px;min-height:80px;display:flex;align-items:center;
			  ul li{float:none;}
			}
		  }
		  &.tb1{width:20%;
			dl{width:100%;
			  dd{width:100%;justify-content:center;
				&:last-child{border-left:0;}
			  }
			  &:after{content:'';display:block;clear:both;}
			}
		  }
		  &.tb2{width:35%;
			dd{text-align:left;
				span{color:#999;}
			}
		  }
		  &.tb3{width:15%;
			dd{justify-content:center;}
		  }
		  &.tb4{width:30%;
			dd{text-align:left;
				> span{display:inline-block;margin-right:15px;}
			}
		  }
		}
		&:after{content:'';display:block;clear:both;}
		&.change{
		  li{
			dl{
			  dt{display:none;}
			}
		  }
		}
	}
	&.nodata {min-height:240px; display:flex; align-items:center; justify-content:center; padding-top:0; border-bottom:1px solid #e4e4e4; font-size:16px;}
}

@include mediaQuery(tablet){
	//반응형 테이블
	.tb-list-wrap{
		.tb-con-res{
			li{
				dl{
					dd{
						button{min-width:auto;padding:13px 10px;}
					}
				}
			}
		}
	}
}


@include mediaQuery(phone){
	//반응형 테이블
	.tb-list-wrap{border-top:1px solid #999;
		> li{border-bottom:0;}
		.tb-con-res{width:100%;
			li{float:none;width:100% !important;
				dl{position:relative;padding-left:83px;border:1px solid #e4e4e4;border-top:0;
					dt{position:absolute;top:0;left:0;width:83px;height:100%;padding:10px 5px;border-bottom:0;text-align:left;font-size:12px;}
					dd{flex-direction:column;align-items:flex-start;height:auto;padding:10px 0;min-height:52px;border-bottom:0;border-left:1px solid #e4e4e4;text-align:left;padding-left:10px;font-size:13px;
						button{min-width:auto;padding:5px 10px;font-size:13px;
							.bar{
								&:after{top:2px;}
							}
							em{font-size:13px;}
						}
					}
				}
				&.tb1{
					dl {
						dd{
							&:last-child{border-left:1px solid #e4e4e4;}
						}
					}
				} 
				&.tb4{
					dd{justify-content:center}
				}
			}
			&.change{margin-top:3px;border-top:1px solid #999;
				li{
					dl{
						dt{display:block;}
					}
				}
			}
		}
	}
}

// 마이페이지 :: 입력
.entry-wrap {margin-top:60px;
	.entry-cont {margin-top:20px; border-top:2px solid #999;
		.lb-wrap { @include lbWrap;
			&.address-type {
				.lb-group {
					.lb-group-inline{ flex:0 1 auto;
						&.epost { margin-right:10px; margin-bottom:10px;
							& + .lb-group {margin-bottom:10px;
								.input-type {width:290px;
									&:not(:first-of-type){margin-left:10px;}
								}
							}
							.input-type {width:195px; }
						}
					}
				}
			}
			.lb-title { @include lbTitle;
				i.star-bullet {vertical-align: middle; margin-left: 5px; color: #cc3333; font-style: normal;}
			}
			.lb-cont {width:100%;}
			.lb-group {display:flex; flex-wrap:wrap;}
			.lb-group-inline {display:flex; flex-wrap:wrap; align-items: center; //margin-top:-10px;
				.input-type {width:280px; margin-right:10px;
					input {width:100%;}
				}
				.input-group.date {width:280px;}
				.form-group {margin-bottom:0;}
				.form-number {width:365px;} /* width:100%; */
				.btn {margin:0;}
			}
			.select-wrap {margin-top:0; margin-bottom:10px;}
			.form-txtarea {height:120px;}
			.form-txtarea.box-type {height: 270px; min-height: 270px;}
			.exp {color: $fc-light; font-size: $fz-sm;}
			&.add-item {
				.lb-title {
					.exp {display:block; font-weight: $fw-n; }
				}
				.lb-cont {
					.exp {display:inline-block; margin:2px 0 0 10px; }
				}
			}
		}
	}
	&.resident-wrap {
		.entry-cont .lb-wrap .lb-group .lb-group-inline span {padding: 0 10px;}
	}
}

// 키워드 추가시 
.keyword-add-list { display:flex; margin-top:15px;
	.btn {margin:0 10px 0 0;}
}


.table-area.column-type.inline-type, .entry-wrap.resident-wrap.tbl-type {
	// th {width: 170px;}
	i.star-bullet {vertical-align: middle; margin-left: 5px; color: #cc3333; font-style: normal;}
}


// 입력폼 하단 설명문구
.lb-group + p, .lb-cont p {margin-top:8px; color: $fc-light; font-size: $fz-sm; padding-left: 0;
	&:before{content:'';}
}
//.lb-group + p {margin-top:0;}


// 리스트
.list-wrap {margin-top:20px;
	.list-cont {margin-bottom:10px; border-top:2px solid #999;
		> li {display:table; width:100%; padding:16px 30px; border-bottom:1px solid #e4e4e4;
			&.nodata {height:240px; padding:100px 0; font-size: $fz-md; font-weight:500; text-align:center;}
		}
		div[class*="-box"] {display:table-cell; vertical-align:middle;}
		div[class*="-box"]:last-child {text-align:right;
			.btn{
				&.disabled{cursor:default;}
			}
		}
		.list-tit { display:block;
			.company {font-size:$fz-md; color:#333; font-weight: $fw-m;
				.badge-type {position:relative; /*top:-2px;*/ margin-left:3px;
					@include mediaQuery(pc_under) {
						&:before {content:''; display:inline-block; position:absolute; left:5px; top:7px; width:1px; height:12px; background:#ddd;}
						height:auto;
						margin-left:0;
						padding:0 0 0 15px;
						color:#666;
					}
				}
			}
			strong {display:block; width:100%; margin-top:8px; word-break:break-all; font-size: $fz-lg; font-weight: $fw-b; letter-spacing:-1.5px; 
				@include common-transition();
			}
			.deadline {margin-top:15px; font-size:$fz-md; color: #666; @include common-transition();
				//.badge-day {position:relative; top:-2px;}
				&.only-date {
					@include mediaQuery(pc_under) {flex-direction: row;}
				}
				@include mediaQuery(pc_under) {
					display: flex;
					align-items: center;
					justify-content: space-between;
					flex-direction: row-reverse;
					color:#666;
				}
			}
		}
	}
}

// 리스트 상단영역
.list-head { @include head-align($pd : 0 0 20px 0);
	@include mediaQuery(pc_under) {
		&.mo-d-n {display:none !important;}
	}
	&.type-tg { padding:0 0 10px;}
	.form-check.form-check-mb.ml-type {margin-left: -175px;}
	.form-check.form-check-mb label {margin-bottom: 0;
		i.lb-txt {font-style: normal; font-size: 16px; color: #999;}
	}
	.other {display:flex; justify-content:left; position: relative;
		.select-wrap {margin-top:-10px;}
		span.sub-txt {color: #cc3333; font-size: 14px; margin-top: 40px;
			&::before {content: "*"; display: inline-block; padding-right: 5px; color: #cc3333; font-size: 14px; vertical-align: middle;}
		}
		button.ico-list-add {width: 24px; height:24px; background:url($svg_add) 0 0 no-repeat;
			.hidden {position: absolute; top:auto; left:-10000px; width:1px; height:1px; overflow:hidden;}
		}
	}
}

.login-wrap {
	.login-cont {
		.lb-wrap {
			@include lbWrap;
			&:first-child {border-top:2px solid #999;}
			.lb-title { @include lbTitle; }
			.inp-wrap {margin-top:0;}
		}	
	}	
}


//하단 배너
.outline-box {overflow:hidden; display:flex; justify-content:space-between; align-items:center; padding:35px 40px; margin-top:60px; border:1px solid #e4e4e4;
	div {display:flex; align-items:center; 
		strong {position:relative; display:inline-block; margin-right:20px; font-size: $fz-lg;
			//궁금합니다 ico
			&.qna {padding-left:48px;
				&:before {content:''; position:absolute; left:0; top:2px; display:inline-block; width:34px; height:32px; background:url($imgpath + 'svg/icon_qna.png') no-repeat left center; }
			}
			//채용공고 확인 ico
			&.career {padding-left:48px;
				&:before {content:''; position:absolute; left:0; top:2px; display:inline-block; width:30px; height:28px; background:url($svg_career) no-repeat left center; }
			}
		} 
		p {font-size: $fz-md;}
	}
}

// 자주하는 질문
.ask-srch-wrap { padding-top:38px; margin-bottom:40px; border:1px solid #e4e4e4; 
	.srch-group { display:flex; width:670px; margin:0 auto;
		.select-wrap {width:240px; margin:0 10px 0;
			.select-item {width:240px;}
		}
		.ip-wrap {position:relative; width:420px;
			input {padding-right:40px;}
			.btn-srch {position:absolute; right:0; top:0; margin:0; }
		}
		&:last-child{padding-bottom:40px;}
	}
	// 자주하는 질문 카테고리
	.regular-category { padding:20px 30px 40px; 
		ul { display:flex; justify-content: center; flex-wrap:wrap;
			li { display:inline-block; overflow:hidden; margin:0 10px 10px 0; border:1px solid #ccc; border-radius:18px; 
				&.on { border-color: $point-color-primary;
					a {color: $point-color-primary; font-weight: $fw-b; background: #eaf8ff;}
				}
				&:last-of-type {margin-right:0;}
				a {display:block; height:36px; line-height:1; padding:10px 15px; background:#fff;}
			}
		}	
	}
	& + .toggle-list-wrap { border-width:1px;}
}


// 공채 온라인상담 상단
.advice-srch-wrap {display:flex; justify-content:space-between; padding:39px 35px; margin-bottom:40px; border:1px solid #e4e4e4;
	.btn-down {display:inline-flex; align-items:center; min-width:auto; padding:0; margin:0; color:#333; font-weight: $fw-m; 
		.ic.down {display:inline-block; position:relative; width:26px; height:26px; margin-left:10px; border-radius:50%; background:#666;
			&:before {content:''; display:block; position:absolute; top:50%; left:50%; width:11px; height:11px; margin-top:-2px; transform:translate(-50%, -50%) rotateZ(135deg); border-radius:0; border:2px solid #fff; border-width:2px 2px 0 0;}
		}
	}
	.srch-group { display:flex;
		.select-wrap {width:240px; margin:0 10px 0;
			.select-item {width:240px;}
		}
		.ip-wrap {position:relative; width:350px;
			input {padding-right:40px;}
			.btn-srch {position:absolute; right:0; top:0; margin:0; }
		}
	}
}

// 인성검사 문항 예시
.test-area {padding:25px 20px; border:1px solid #e4e4e4;
	> div {padding-bottom:30px; margin-bottom:25px; border-bottom:1px solid #e4e4e4;
		&:last-child {border:0; padding-bottom:0; margin-bottom:0;}
		dt {font-weight: $fw-b;}
		dd {display:flex; margin-top:12px; 
			.choice-wrap {display:flex; align-items: center; 
				&:not(:last-of-type) {margin-right:40px;}
				.lb-title {margin-right:12px; font-weight: $fw-b; color: $fc-light; font-size: $fz-sm;}
				.btn-group {display:flex;
					&.btn-text {
						.btn {min-width:90px;}
					}
					.btn {padding:8px 20px; margin-top:0; font-size: $fz-md; border-color:#ccc !important;
						&.active {color: $fc-point; font-weight: $fw-b; background:#eaf8ff;}
					}
				}
			}
		}
	}
}

