$modal-paddingX : 30px;
$modal-paddingX-mobile : 15px;
$modal-fix-bodyH : 181px;  //110+71

.close{opacity:1;}
.modal-backdrop.show{opacity:0.6;}




.wpop,
.modal{
	& .modal-dialog{max-width:430px; margin: 0 auto; padding-left: 15px; padding-right: 15px; }//default
	&.modal-md.modal-video .modal-dialog{max-width:1040px;margin:0 auto;}
	&.modal-md .modal-dialog{max-width:740px;margin:0 auto;}
	&.modal-ms .modal-dialog{max-width:540px;margin:0 auto;}
	&.modal-mx .modal-dialog{max-width:400px;margin:0 auto;}
	&.modal-xs .modal-dialog{width:286px;margin:0 auto;}

	.modal-content{background:none; border:none;
		&.system{
			.modal-footer{padding-bottom:40px;}
		}
	}

	//header
	.modal-header{padding:25px $modal-paddingX 15px; border-bottom-color: #ddd;border-radius:0;background-color:#fff;
		@include mediaQuery(phone){
			padding-left:$modal-paddingX-mobile; padding-right:$modal-paddingX-mobile;
		}
		.modal-title{position: relative;/*margin:0 auto;*/ /*text-align: center;*/font-size:$fz-lg;font-weight:$fw-b;line-height: 1.2;word-break: break-all;}
		.btn.close {padding:10px; margin:-10px;min-width: 0;
			.ic-close{display:block;background:url($svg_modal_close) center center no-repeat;width:30px;height:30px;}
		}
	}
	//body
	.modal-body{@include clearfix; padding:30px 0 0px; background-color:#fff;
		//default fix
		overflow-y:auto;
		max-height:calc(100vh - ( #{$modal-fix-bodyH} + 80px)); // 155

		.modal-icon{color: #f8bb86; position: relative; justify-content: center; width: 90px; height: 90px; margin: 12px auto 20px; line-height: 5em; cursor: default;
			box-sizing: content-box;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
			zoom: normal;
			background: url($svg_modal_success) 50% 50% no-repeat;
			&.warning{width:100px;height:100px; background: url($svg_modal_warning) 50% 50% no-repeat; }
			&.danger{background: url($svg_modal_danger) 50% 50% no-repeat; }
			&.completed{width:60px;height:60px;background:#31cda9 url($svg_check_large) 50% 50% no-repeat;border-radius:50%; }
		}

		.modal-wrap{padding-left:$modal-paddingX; padding-right:$modal-paddingX; @include clearfix(); padding-bottom:40px;
			@include mediaQuery(phone){
				padding-left:$modal-paddingX-mobile; padding-right:$modal-paddingX-mobile;
			}
			& + .modal-wrap{margin-top: 20px;}
			.box{padding:0;
				.box-con{box-shadow:none !important;}
				.box-header, .box-body, .box-footer{padding:0;box-shadow:none !important;}
			}
			// 로그인 팝업(이메일인증,완료안내)
			&.login{
				.box-wrap{width:100%;height:76px;background:#f6f6f6;text-align:center;margin-bottom:20px;
					span{line-height:76px;border-bottom:1px solid #333;}
				}
				.form-group{
					.fc-s{font-size:16px;}
				}
				.completed-wrap{
					p{font-size:20px;text-align:center;
						&.value{font-weight:700;text-decoration: #333;
							em{border-bottom:1px solid #333;}
						}
					}
				}
				.notice{border-top:1px solid #ddd;padding-top:30px;margin-top:35px;
					.h4-title{font-size:16px;font-weight:800;}
					p{text-align:left;font-size:16px;color:#999;margin-top:5px;}
				}
			}
		}

		.modal-con{font-size:14px;color:#777; }
		.modal-con-box{font-size:14px;color:#777; background-color:#f2f3f8; padding:20px 30px; }
		.img-area{margin-top:30px; text-align:center; margin:0 auto;
			img{display:inline-block;min-height:100%;max-width:220px; border:1px solid #d2d2d2;}
		}
		.modal-txt{text-align:center;}
	}

	//footer
	.modal-footer{display: block;border-top:none; padding:0px $modal-paddingX 30px; background-color:#fff; 
		@include mediaQuery(phone){
			padding-left:$modal-paddingX-mobile; padding-right:$modal-paddingX-mobile;
		}
		.form-check{position:absolute; right:5px; bottom:10px; }
	}

	&.modal-video{
		.modal-header{border-bottom-color:#fff;padding:25px 30px 0;}
		.modal-body{padding:20px 0 0;}
	}
}


//모바일 전체 화면

html:not(.pc-only) .modal.mobile-full{
	@include mediaQuery(phone){
		.modal-dialog{width:100vw; max-width:100vw;height:100vh;margin:0 auto;padding: 0;}
		.modal-content{height: 100vh;}
		.modal-body{overflow-y:auto;
			height:calc(100vh - #{$modal-fix-bodyH}); 
			max-height:100%; 
		} // 155
	}
}


// window popup
.wpop{
	.modal-body{max-height:100%;}
}


//modal iframeFull
.wpop.iframe-full,
.modal.iframe-full{
	.modal-body{padding:0;max-height:100%;
		.modal-wrap{padding-left:0; padding-right:0; padding-bottom:0;}
	}
}


//IE patch 
html.ie .modal.show .modal-dialog {
	height: calc(100% - (0.5rem * 2));
}

@include mediaQuery(phone){
	html.ie .modal.mobile-full .modal-dialog{
		height: 100%;
	}
	.modal-video{
		.modal-header{padding:20px 15px 0 !important;}
	}
}


