@charset "utf-8";

/**
* Work
* ------------------
* 래영
*/
.grid{
	&::after{content:'';display:block;clear:both}
	>li, >div{float:left;position:relative}
	&.s1>li, &.s1>div{width:100%}
	&.s2>li, &.s2>div{width:50%}
	&.s3>li, &.s3>div{width:33.3333%}
	&.s4>li, &.s4>div{width:25%}
}
.modal .modal-dialog{
    &.dbs{max-width:740px}
}
.dbs_section{
	h3, h3.ex{margin-top:32px;font-weight:$fw-b;font-size:$fz-lg}
	h3.point{margin-top:0;font-weight:$fw-b;font-size:$fz-md;color:#cc3333;}
    .table-area{border:none;margin-top:25px}
    .table-area tbody th{vertical-align:top;/*font-size:$fz-sm;*/color:$fc-base;padding:18px 0 0 0;}
    .table-area tbody td{padding:10px 0;
        input[type="text"], textarea{width:100%;}
    }
	.table-area tbody th, .table-area tbody td{border:none}
	.table-area .form-number {display: flex; flex-wrap: nowrap;
		span {display: inline-block; padding: 11px 10px;}
		.form-group {flex-grow: 1 auto;
			input {min-width: 170px;}
		}
	}
	.dbs_img{margin-top:40px;
		img{max-width:100%;}
	}
	.dbs_ad_view{margin-top:10px;padding:30px 28px;font-size:$fz-sm;color:$fc-base;border:1px solid #e4e4e4;}
	.dbs_ad_txt{font-size:$fz-sm;color:$fc-light;
		b{color:$fc-base;}
	}
	.dbs_view{font-size:$fz-md;color:$fc-base;text-align:center;
		&.auto{
			p{margin-bottom:15px;}
		}
		span{font-weight:$fw-m;color:$fc-point;}
		em{display:block;margin-bottom:20px;font-size:$fz-md;font-weight:$fw-m;}
	}
	dl.auto-timeout{display:flex;justify-content:center;align-items:center;padding:30px 0;margin-top:30px;text-align:center;font-size:$fz-sm;background:#f6f6f6;
		dt{margin-right:10px;font-weight:$fw-n;font-size:$fz-sm;}
		dd{font-size:$fz-lg;color:#cc3333;}
	}
}
.dbs_head{
    h3, p{font-size:$fz-lg;
        strong{font-weight:$fw-n;color:$point-color-primary}
    }
    p.s{margin-top:6px;font-size:$fz-sm;color:#666}
	h3+p.s{margin-top:12px}
	span span.txt {color: #117ada;}
}
.dbs_guide_box{/*max-height:590px;*/padding:30px;border:1px solid #e4e4e4;overflow-x:hidden;//overflow-y:auto;
    .dbs_guide+.dbs_guide{margin-top:23px}
    .dbs_guide{
        h3{font-weight:$fw-b;font-size:$fz-lg;
        	&:first-child{margin-top: 0;}
        }
        ul{margin-top:7px;
            li{position:relative;/*font-size:$fz-md;*/padding:3px 0 3px 10px;color:$fc-base;
                &:before{content:'';position:absolute;top:14px;left:0;width:4px;height:1px;background-color:$fc-base;border-radius:100%}
               strong{font-weight:$fw-n;color:$point-color-primary}
               a{display:inline-block;color:$point-color-primary;text-decoration:underline}
            }
        }
    }
}
.dbs_guide_s{margin-top:11px;
    li{position:relative;padding:4px 0;font-size:$fz-sm;color:$fc-light;
		&.star{padding-left:10px}
		&.star:before{content:'*';position:absolute;top:7px;left:0}
	}
}
h3.ex+.dbs_q_box{margin-top:20px}
.dbs_q_box{
    padding:30px;border:1px solid #e4e4e4;
    .dbs_q+.dbs_q{margin-top:30px}
    h4{position:relative;padding:0 0 15px 38px;border-bottom:1px solid #e4e4e4;line-height:26px;font-weight:$fw-b;font-size:$fz-md;
        i{position:absolute;top:0;left:7px;font-style:normal;}
    }
    ul{margin-top:18px;
        // li{font-size:$fz-sm}
    }
}
.dbs_status{position:relative;border:1px solid #e4e4e4;padding:15px 30px;margin:20px 0 0;
    .time{display:inline-block;position:absolute;top:20px;right:30px;padding-left:26px;font-size:$fz-md;font-weight:$fw-m;
        &::before{content:'';position:absolute;top:3px;left:0;width:20px;height:18px;background-image: url("data:image/svg+xml,%0A%3Csvg width='20' height='18' xmlns='http://www.w3.org/2000/svg'%3E%3C!-- Created with Method Draw - http://github.com/duopixel/Method-Draw/ --%3E%3Cg%3E%3Ctitle%3Ebackground%3C/title%3E%3Crect fill='%23fff' id='canvas_background' height='20' width='22' y='-1' x='-1'/%3E%3Cg display='none' overflow='visible' y='0' x='0' height='100%25' width='100%25' id='canvasGrid'%3E%3Crect fill='url(%23gridpattern)' stroke-width='0' y='0' x='0' height='100%25' width='100%25'/%3E%3C/g%3E%3C/g%3E%3Cg%3E%3Ctitle%3ELayer 1%3C/title%3E%3Cline stroke-linecap='undefined' stroke-linejoin='undefined' id='svg_6' y2='4.18765' x2='19.12512' y1='0.71876' x1='15.65624' stroke-width='1.5' stroke='%23000' fill='none'/%3E%3Cline stroke-linecap='undefined' stroke-linejoin='undefined' id='svg_7' y2='0.93736' x2='4.06266' y1='4.09376' x1='0.90626' fill-opacity='null' stroke-opacity='null' stroke-width='1.5' stroke='%23000' fill='none'/%3E%3Cellipse stroke='%23000' ry='7.28124' rx='7.28124' id='svg_8' cy='9.9375' cx='10' fill-opacity='null' stroke-opacity='null' stroke-width='1.5' fill='none'/%3E%3Cline stroke-linecap='null' stroke-linejoin='null' id='svg_10' y2='9.96875' x2='9.09322' y1='9.96875' x1='12.78124' fill-opacity='null' stroke-opacity='null' stroke-width='1.5' stroke='%23000' fill='none'/%3E%3Cline stroke-linecap='null' stroke-linejoin='null' id='svg_13' y2='10.53125' x2='9.78125' y1='7.15625' x1='9.03125' fill-opacity='null' stroke-opacity='null' stroke-width='1.5' stroke='%23000' fill='none'/%3E%3C/g%3E%3C/svg%3E");}
        strong{padding-left:8px;font-weight:$fw-m;color:#cc3333}
    }
    .total{font-size:16px;font-weight:$fw-m;color:$fc-base;
        >span{padding-left:5px;font-size:$fz-lg;
            >strong{font-weight:$fw-m;color:$fc-point;}
        }
    }
    .chart{position:relative;padding:15px 0 30px;
        .bar{position:relative;height:20px;background-color:#e0e0e0;
            >span{position:absolute;top:0;left:0;height:20px;background-color:$fc-point;transition:0.4s}
        }
        ul{display:flex;display:-webkit-flex;
            li{-webkit-box-flex:1;-webkit-flex:1 100%;flex:1 100%;position:relative;font-size:$fz-xs;font-weight:$fw-m;color:$fc-light;text-align:center;
                i{position:absolute;top:8px;right:0;width:60px;margin-right:-30px;font-style:normal}
                &:first-child{-webkit-box-flex:0;-webkit-flex:0 0;flex:0 0}
                &:first-child>i{left:0;right:inherit;width:auto;margin:0;text-align:left}
                &:last-child>i{width:auto;margin:0;text-align:right}
            }
        }
    }
}
.dbs_guide_form_item{margin-top:40px;
	>h3+.dbs_guide_form{margin-top:20px}
}
.dbs_guide_form{border-width:1px 0;border-style:solid;border-color:#e4e4e4;padding:20px 0;
    >ul>li{display:table;width:100%;padding:5px 0;
        strong{font-weight:$fw-m;color:$fc-point;}
        .lb, .value{display:table-cell;}
        .lb{width:130px;/*font-size:$fz-sm;*/font-weight:$fw-m;}
        .value{/*font-size:$fz-sm;*/font-weight:$fw-n;
            >ul>li.dh{position:relative;padding:0 0 0 8px;
                &::before{content:'';position:absolute;top:12px;left:0;width:4px;height:1px;background-color:#333}
            }
			>ul>li.rm{position:relative;padding-left:18px;
				&::before{content:'※';position:absolute;top:0;left:0}
			}
        }
    }
	>ul.lb90 .lb{width:90px}
	a.link{display:inline-block;color:$point-color-primary;text-decoration:underline}
}
.dbs table{font-size:16px}
.table-area.dbs{border-bottom:none;
	tbody tr:last-child th, tbody tr:last-child td{border-width:0 1px 1px 0;border-style: solid;border-color:#e4e4e4}
	th, td{border-width:0 1px 1px 0;border-style: solid;border-color:#e4e4e4;vertical-align:middle}
	th{width:auto;background-color:#f6f6f6}
	.q{padding:30px;}
	.form-check-inline{margin-right:20px;
		&:last-child{margin-right:0;}
		input[type="radio"] + label {padding-left:25px;}
	}
	.nlr{border-right:none!important}
	label.sg{padding-left:24px}
	.ct{text-align:center!important}
}
.ex_dbs_question{
	.img>img{width:100%}
	h3.q{line-height:26px;margin-top:25px;padding-bottom:15px;border-bottom:1px solid #e4e4e4;font-size:18px;
		strong{font-weight:$fw-n;color:$point-color-primary}
	}
	ul.a_list{margin-top:20px;
		li{display:inline-block;padding:6px 30px 6px 0}
	}
}
