.find_contents{
	.search_form{position:relative;
		input{width:100%;padding-right:40px;}
		button{position:absolute;top:0;right:0;width:48px;height:48px;background:url($svg_ico_srch) center center no-repeat}
	}
	.find_contents_list{border-width:0 1px 1px;border-style:solid;border-color:#e4e4e4;
		.scroller{max-height:205px;padding:10px 15px 15px;overflow-y:auto;overflow-x:hidden}
		a{display:block;padding:2px 0;font-size:$fz-md}
		a>strong{font-weight:$fw-n;color:$point-color-primary}
		.no_data{position:relative;padding:50px 0;font-weight:$fw-n;text-align:center}
		.no_data::before{content:'';display:block;width:32px;height:31px;margin:0 auto 15px;background:url($svg_ico_nodata) center center no-repeat}
	}
	.direct_input{position:relative;margin-top:20px;
		label{display:inline-block;width:73px}
		input{display:inline-block;width:274px}
		.btn{position:absolute;top:0;right:0;min-width:90px;margin:0}
	}
}

.file_upload{position:relative;margin-top:5px;padding-right:106px;
	.txt{min-width:100%!important;background-color:#fff!important}
	.click{position:absolute;top:0;left:0;right:0;bottom:0;margin-bottom:0}
	.btn{position:absolute;top:0;right:0;min-width:90px;margin:0;border:1px solid #999;cursor:pointer;text-align:center}
	.btn span{display:inline-block;position:relative;padding-left:22px;text-align:left}
	.btn span::before, .btn span::after{content:'';position:absolute;top:50%;left:0;width:12px;height:2px;margin-top:-1px;background-color:#666}
	.btn span::after{transform:rotate(90deg)}
}
.dbs_box{margin-top:20px;border:1px solid #e4e4e4;
	.scroller{max-height:500px;padding:30px;overflow-x:hidden;overflow-y:auto}
	.box_wrap{padding:30px;overflow-x:hidden;overflow-y:auto}
}
.index_list+.index_list{margin-top:30px}
.index_list{
	h4{font-size:$fz-lg;font-weight:$fw-m;color:$fc-point}
	>ul{margin-top:12px;
		>li+li{margin-top:10px}
		>li{position:relative;font-size:$fz-md;font-weight:$fw-n;
			>ul{margin-top:6px;
				>li{position:relative;font-weight:$fw-l;
					>ul{
						>li{position:relative;font-size:$fz-md;font-weight:$fw-l}
						>li.dot{padding-left:5px}
						>li.dot::before{content:'';position:absolute;top:12px;left:0;width:2px;height:2px;background-color:#5c5c5c;border-radius:100%}
					}
				}
				>li.dh{padding-left:7px}
				>li.dh::before{content:'';position:absolute;top:12px;left:0;width:4px;height:1px;background-color:#333}
			}
		}
		>li.no{padding-left:15px}
		>li.no>i{position:absolute;top:0;left:0;font-style:normal}
	}
}
@include mediaQuery(phone){
	.find_contents{
		.direct_input{padding-right:100px;
			label{display:block}
			input{display:inline-block;min-width:100%!important;width:100%!important}
			.btn{position:absolute;top:inherit;right:0;min-width:90px;margin:0}
		}
	}
}
/*일하기 좋은*/
.dpb_section{
	.dpb_head{
		h3{font-size:$fz-lg;font-weight:$fw-b;color:$fc-point}
		p{margin-top:3px;font-size:$fz-lg}
	}
	.dpb_content{margin-top:30px;
		.img img{width:100%}
		.img+p{margin-top:35px}
