/*직무 알아보고*/
.job_viewer{
	h2{font-size:30px;font-weight:$fw-b}
}
.job_filter{margin-top:30px;border:1px solid #e4e4e4;
	.job_search{text-align:center;padding:35px 0;
		select{display:inline-block;width:240px;margin-right:8px}
		button{width:100px}
	}
	.job_sorting{position:relative;border-top:1px solid #e4e4e4;padding-right:90px;
		ul{display:flex;display:-webkit-flex}
		li{-webkit-box-flex:1;-webkit-flex:1 100%;flex:1 100%;
			button{position:relative;width:100%;font-weight:$fw-m}
			button>span{display:inline-block;position:relative;height:60px;line-height:58px}
			button.active::after{content:'';position: absolute;bottom:0;left:0;width:100%;height:6px;background-color:$fc-point}
		}
		.btn_toggle{position:absolute;top:0;right:0;width:90px;height:60px;padding-left:18px;border-left:1px solid #e4e4e4;font-size:$fz-sm;color:#666;text-align:left;
			&::after{content:'';position:absolute;top:50%;right:18px;width:8px;height:8px;margin:-5px 0 0 0;border-width:0 0 2px 2px;border-style:solid;border-color:#666;transform:rotate(-45deg);transition:0.4s}
		}
	}
}
.job_filter.show .btn_toggle::after{margin-top:-2px;transform:rotate(135deg)}
.biz_class_list{display:none;padding:30px 50px;border-top:1px solid #e4e4e4;background-color:#fafafa;text-align:center;
	.head, .btm_set{display:none}
}
.job_filter.show .biz_class_list{display:block}
.biz_class_item{position:relative;padding:4px 0 4px 126px;text-align:left;
	h3{position:absolute;top:18px;left:0;font-size:16px;font-weight:$fw-b}
	li{position:relative;display:inline-block;padding:4px 3px;
		input[type="checkbox"] + label{min-width:130px;min-height:36px;line-height:36px;margin:0;padding:0 20px;border:1px solid #dfdfdf;background-color:#fff;font-size:$fz-sm;border-radius:36px;cursor:pointer;text-align:center}
		input[type="checkbox"] + label::before, input[type="checkbox"] + label::after{display:none}
		input[type="checkbox"]:checked + label{border-color:#519ee4;background-color:#eaf8ff;font-weight:$fw-b;color:$fc-point}
	}
}
.job_list{margin-top:80px;border-top:2px solid #333;
	.jobs{margin:20px -10px 0}
	.jobs::after{content:'';display:block;clear:both}
	.jobs li{float:left;padding:10px;width:33.3333%}
	.jobs li>a{display:table;position:relative;width:100%;height:200px;
		>span{display:table-cell;vertical-align:middle;padding:0 30px;
			.job_detail{display:inline-block;font-weight:$fw-m}
			.job_group{display:inline-block;height:26px;line-height:26px;margin-left:8px;padding:0 15px;background-color:#eee;border-radius:26px}
			.line{display:block;margin-top:11px;font-weight:$fw-b;font-size:30px}
		}
		&::before{content:'';position:absolute;top:0;left:0;right:0;bottom:0;border:1px solid #e4e4e4}
		/*&.active{box-shadow:5px 5px 5px 1px rgba(0,0,0,0.1)}
		&.active::before{border:2px solid $fc-point}
		&.active::after{content:'';position:absolute;top:2px;right:2px;width:58px;height:58px;background-color:$fc-point}
		&.active>span::before, &.active>span::after{content:'';position:absolute;top:18px;right:29px;width:2px;height:24px;background-color:#fff;z-index:10}
		&.active>span::after{transform:rotate(90deg)}*/

		&:hover{box-shadow:5px 5px 5px 1px rgba(0,0,0,0.1);transition:all .2s ease-in;}
		&:hover::before{border:2px solid $fc-point;}
		&:hover::after{content:'';position:absolute;top:2px;right:2px;width:58px;height:58px;background-color:$fc-point;transition:all .1s ease-in-out;}
		&:hover>span::before, &:hover>span::after{content:'';position:absolute;top:18px;right:29px;width:2px;height:24px;background-color:#fff;z-index:10;}
		&:hover>span::before{animation:spin01 .3s forwards;}
		&:hover>span::after{animation:spin02 .3s forwards;}
	}
	@keyframes spin01 {
		0% {transform: rotate(0);}
		100% {transform: rotate(180deg);} 
	}
	@keyframes spin02 {
		0% {transform: rotate(0);}
		100% {transform: rotate(90deg);} 
	}
	  
}
