.main_visual{position:relative;
	.visual_swipe{overflow:hidden;
		.swiper-slide{position:relative;overflow:hidden;
			.mobile{display:none}
			>.img{display:block;height:700px;overflow:hidden;background-repeat:no-repeat;background-position:50% 50%;background-size:cover;transform:scale(1.2) rotate(.001deg);transition:5s}
			&.swiper-slide-active>.img{transform:scale(1)}
			&.v1>.img{background-image:url("../../assets/img/main/main_v1.jpg")}
			&.v2>.img{background-image:url("../../assets/img/main/main_v2.jpg")}
			&.v3>.img{background-image:url("../../assets/img/main/main_v3.jpg")}
			&.v4>.img{background-image:url("../../assets/img/main/main_v4.jpg")}
			&.v5>.img{background-image:url("../../assets/img/main/main_v5.jpg")}
			&.v6>.img{background-image:url("../../assets/img/main/main_v6.jpg")}
			.slogan{position:absolute;top:67px;left:50%;width:1200px;margin-left:-800px;
				&::before{content:'';position:absolute;top:30px;left:30px;width:225px;height:225px;transform:scale(0);opacity:0}
				&>span{position:absolute;top:81px;left:75px;right:0;line-height:70px;font-size:54px;font-weight:$fw-b;color:#fff;letter-spacing:-4.0px;opacity:0;transform:translateY(60px);transition:1.5s ease-out}
			}
			&.v1 .slogan::before, &.v4 .slogan::before{background-color:#0018a8}
			&.v2 .slogan::before, &.v5 .slogan::before{background-color:#0088ce}
			&.v3 .slogan::before, &.v6 .slogan::before{background-color:#00ad83}
			&.v1.swiper-slide-active .slogan, &.v4.swiper-slide-active .slogan{
				&::before{animation:squareDO 2s forwards}
			}
			&.v2.swiper-slide-active .slogan, &.v5.swiper-slide-active .slogan{
				&::before{animation:squareOS 2s forwards}
			}
			&.v3.swiper-slide-active .slogan, &.v6.swiper-slide-active .slogan{
				&::before{animation:squareAN 2s forwards}
			}
			&.swiper-slide-active{
				.slogan{
					&>span{transition-delay:1s;opacity:1;transform:translateY(0)}
				}
			}
		}
	}
	@keyframes squareDO{
		0%{transform:scale(0);opacity:0}
		50%{transform:scale(1);opacity:1}
		100%{transform:rotate(18deg);opacity:1}
	}
	@keyframes squareOS{
		0%{transform:scale(0);opacity:0}
		50%{transform:scale(1);opacity:1}
		100%{transform:rotate(-4.2deg);opacity:1}
	}
	@keyframes squareAN{
		0%{transform:scale(0);opacity:0}
		50%{transform:scale(1);opacity:1}
		100%{transform:rotate(6.3deg);opacity:1}
	}
	.indicator{position:absolute;bottom:82px;left:50%;width:1200px;margin-left:-800px;z-index:100;
		.visual_pagination{display:inline-block;position:relative}
		.swiper-pagination-bullet{position:relative;width:80px;height:4px;margin-right:10px;border-radius:0;background:none;opacity:1;
			&::before{content:'';position:absolute;top:0;left:0;height:4px;width:100%;background-color:#fff;opacity:0.3}
			&::after{content:'';position:absolute;top:0;left:0;height:4px;width:0;background-color:#fff}
			&.swiper-pagination-bullet-active::after{animation:pageBar 6s linear forwards}
		}
		
		>button{position:relative;width:20px;height:20px;margin:0 0 0 -9px;
			&::before, &::after{content:'';position:absolute;top:50%;left:50%;width:0;height:0;margin:-4px 0 0 -3px;border-style:solid;border-width:4px 1px 4px 1px;border-color:#fff;transition:0.4s}
			&::after{margin-left:1px}
			&.play::before{border-width:4px 0 4px 8px;border-color:transparent transparent transparent #fff}
			&.play::after{transform:scale(0)}
		}
	}
	@keyframes pageBar{
		0%{width:0}
		100%{width:100%}
	}
}
