			&::after{content:'';position:absolute;top:50%;right:20px;width:40px;height:40px;margin-top:-20px;background-size:40px 40px}
			span{line-height:26px;margin-top:-3px;font-size:18px}
		}
		&::after{top:-50px;right:20px;width:110px;height:30px;background-size:110px 30px;}
	}
	.main_recruit_list{padding:10px 0 10px;
		>ul>li>a{display:block;position:relative;height:130px;padding:15px 15px 15px 15px;box-shadow:0px 1px 4px 0px rgba(0,0,0,0.15);
			h3{font-size:12px;
				.tag{height:19px;line-height:19px;margin-left:5px;margin-top:-4px;padding:0 8px;border-radius:19px;font-size:11px}
			}
			.recruit_h{margin-top:8px;font-size:$fz-md;padding-bottom:18px;}
			.date{bottom:15px;left:15px;font-size:12px;
				i{height:18px;line-height:18px;padding:0 5px;margin-right:5px;font-size:12px;border-radius:18px}
			}
			.btn_more{bottom:15px;right:15px;font-size:12px;
				&::after{width:7px;height:7px;top:7px;border-width:1px 1px 0 0;}
			}
		}
		>.btn_more{width:180px;height:40px;
			>span{line-height:38px;padding-right:15px;font-size:$fz-sm;
				&::after{top:17px;right:2px;width:7px;height:7px;border-width:1px 1px 0 0}
			}
		}
	}
	.main_item{
		>.inner{padding:80px 15px 40px;
			&.pd_recruit{padding:80px 15px 40px}
		}
	}
	.main_review{
		.swiper-slide{width:680px;}
	}
	.main_ds_profile{height:auto;padding-left:0;
		> .img{position:relative; width:100%;}
	}
}
@include mediaQuery(phone){
	.main_visual{
		.visual_swipe{
			.swiper-slide{
				>.img{height:auto}
				.mobile{display:block;position:relative;
					img{width:100%}
					.txt{position:absolute;top:60px;left:50px;line-height:42px;font-size:32px;font-weight:900;color:#fff;opacity:0;transform:translateY(50px);transition:1.5s}
					.txt::before{content:'';position:absolute;top:-20px;left:-20px;width:120px;height:120px;transform:scale(0);opacity:0;z-index:-1}
				}
				&.v1 .mobile .txt::before, &.v4 .mobile .txt::before{background-color:#0018a8}
				&.v2 .mobile .txt::before, &.v5 .mobile .txt::before{background-color:#0088ce}
				&.v3 .mobile .txt::before, &.v6 .mobile .txt::before{background-color:#00ad83}
				&.v1.swiper-slide-active .txt, &.v4.swiper-slide-active .txt{
					&::before{animation:squareMDO 2s forwards}
				}
				&.v2.swiper-slide-active .txt, &.v5.swiper-slide-active .txt{
					&::before{animation:squareMOS 2s forwards}
				}
				&.v3.swiper-slide-active .txt, &.v6.swiper-slide-active .txt{
					&::before{animation:squareMAN 2s forwards}
				}
				.slogan{display:none}
				&.swiper-slide-active{
					.mobile{
						.txt{opacity:1;transform:translateY(0)}
					}
				}
			}
		}
		@keyframes squareMDO{
			0%{transform:scale(0);opacity:0}
			50%{transform:scale(1);opacity:1}
			100%{transform:rotate(18deg);opacity:1}
		}
		@keyframes squareMOS{
			0%{transform:scale(0);opacity:0}
			50%{transform:scale(1);opacity:1}
			100%{transform:rotate(-4.2deg);opacity:1}
		}
		@keyframes squareMAN{
			0%{transform:scale(0);opacity:0}
			50%{transform:scale(1);opacity:1}
			100%{transform:rotate(6.3deg);opacity:1}
		}
		.indicator{position:absolute;bottom:49px;left:0;width:100%;margin-left:0;z-index:100;text-align:center;
			.swiper-pagination-bullet{width:50px;height:4px;margin-right:5px}
		}
	}
	.main_job_status{position:absolute;top:-25px;left:15px;right:15px;height:50px;padding-right:0;box-shadow:0px 5px 20px 0px rgba(0,0,0,0.15);
		>ul::after{content:'';display:block;clear:both}
		>ul>li{line-height:46px;padding:0 10px}
		>ul>li::after{top:17px;height:18px}
		>ul>li>.lb{font-size:12px; /* display:inline-block;width:80%;vertical-align:middle;line-height:1;word-break:break-all;hyphens:auto;-ms-hyphens:auto;-moz-hyphens:auto; */
			.pc{display:none}
			.mobile{display:inline-block;line-height:1.2;vertical-align:middle;}
		}
		>ul>li>.value{top:2px;right:8px;font-size:18px}
		>a.btn_apply{position:relative;top:inherit;right:inherit;width:100%;height:50px;line-height:50px;margin:43px 0 0 15px;padding-left:20px;
			&::after{content:'';position:absolute;top:50%;right:20px;width:24px;height:24px;margin-top:-12px;background-size:24px 24px}
			span{line-height:50px;font-size:16px;
				>i{display:inline}
			}
		}
		&::after{top:-90px;}
	}
	.main_item{
		>.inner{padding:80px 15px 40px;
			&.pd_recruit{padding:80px 15px 15px;}
			>h2{font-size:$fz-lg}
			>p{margin-top:14px;font-size:$fz-sm;
				>i{display:block}
			}
			.swipe_sort_section{margin-top:30px}
			.sort_swipe_wrap{margin-top:15px}
		}
	}
	.main_review{margin:26px 0 -50px;padding:0;
		.review_swipe{position:relative;overflow:hidden;padding-bottom:50px;
		}
		.swiper-slide{width:200px;padding:0}
		.swiper-slide>a{display:block;position:relative;height:220px;padding-left:0;overflow:hidden;box-shadow:0px 5px 10px 0px rgba(0,0,0,0.15);
			.img{position:absolute;top:0;left:0;width:200px;height:140px;
				&::before{display:none}
			}
			.img>img{width:200px}
			.info{position:absolute;bottom:0;left:0;width:100%;height:75px;padding:10px 15px 15px;background-color:#fff;
				.name{font-size:$fz-sm;
					>span{display:none}
				}
				.department{height:18px;font-size:12px;}
				.review, .btn_more{display:none!important}
			}
		}
		.swiper-slide.swiper-slide-active>a{box-shadow:0px 5px 10px 0px rgba(0,0,0,0.15)}
		.swiper-slide.swiper-slide-prev>a, .swiper-slide.swiper-slide-next>a{height:220px;margin-top:0;
			.img>img{height:auto;opacity:1;filter:none}
			.info{position:absolute;top:inherit;
				.name{position:relative;left:inherit;bottom:inherit;text-align:left!important;font-size:$fz-sm;
					>span{display:none}
				}
				.department{display:block!important}
			}
		}
		.control{display:none}
	}
	.main_news{margin-top:30px;padding:0;
		.news_swipe{position:relative;overflow:hidden}
		.swiper-slide{padding:0;
			h3{margin-top:13px;font-size:$fz-sm}
			ul{margin-top:6px}
			ul>li{font-size:12px}
			ul>li:last-child{padding-left:17px;
				&::before{top:6px;left:8px;height:8px}
			}
		}
		.control{display:none}
		.news_pagination{display:block}
	}
	.main_recruit_list{
		>ul{margin:0}
		>ul>li{width:100%;padding:15px 0 0}
		>.btn_more{margin:30px auto 0}
	}
	.main_item .swipe_sort_section{
	 	.sort{
	 		li{padding:0 0 5px 0}
	 		button{height:30px;line-height:28px;padding:0 8px;border-radius:30px;font-size:12px}
	 	}
	 }
	.main_ds_profile{height:auto;margin-top:15px;padding-left:0;
		>.img{position:relative;top:inherit;left:inherit;width:100%}
		>.img>.txt{bottom:30px;
			.name{font-size:21px}
			.department{font-size:$fz-sm}
		}
		>.img>img{width:100%}
		.schedule{display:none;} 
	}
}