//메뉴 Hover 색상 변경
@mixin menu_hover($color:#f7f7f7, $darken:10%) {
	text-decoration:none;
	background-color: darken($color, $darken);
	@include transition(all .25s ease-in-out);
}

@mixin btn_hover($color:#f7f7f7, $darken:10%) {
	&:hover, &:focus{
		text-decoration:none;
		background-color: darken($color, $darken);
		border-color: darken($color, $darken);
		@include transition(all .25s ease-in-out);
	}
}

@mixin btn_hover_black($color:#f7f7f7, $darken:10%) {
	&:hover, &:focus{
		text-decoration:none;
		background-color: lighten($color, $darken);
		color: #fff;
		@include transition(all .25s ease-in-out);
	}
}

@mixin txt_hover($color:#f7f7f7) {
	// &:hover,&:focus{
		text-decoration:none;
		color: darken($color, 3%);
		@include transition(all .25s ease-in-out);
	// }
}

// radius 
@mixin radius{
	-khtml-border-radius:2px;-moz-border-radius:2px;-webkit-border-radius:2px;border-radius:2px;
}

//compass/@include border-radius 대체
@mixin border-radius($_val:2px){
	-khtml-border-radius:$_val;-moz-border-radius:$_val;-webkit-border-radius:$_val;border-radius:$_val;
}


// * 표시
@mixin icoStar {
	content:'*'; 
	display:inline-block; 
	position:absolute; 
	left:0; 
	top:3px; 
	color: $fc-light; 
	font-size: $fz-xs;
}

// · 표시
@mixin icoDot {
	content:''; 
	display:inline-block; 
	position:absolute; 
	left:0; 
	top:12px; 
	width:2px;
	height:2px;
	background:#afafaf;
}

// lb-wrap 
@mixin lbWrap {
	display:flex; 
	align-items:center; 
	width:100%; 
	padding:16px 30px; 
	border-bottom:1px solid #e4e4e4;
	@include mediaQuery(pc_under) {
		padding:16px 0; 
	}
}

// lb-title
@mixin lbTitle {
	align-self:flex-start; 
	min-width:205px; 
	padding-top:12px; 
	font-size: $fz-md; 
	color: $fc-base; 
	font-weight: $fw-b;
}

// list or input top section 
@mixin head-align ($pd : 20px 0) {
	display:flex; 
	justify-content: space-between; 
	align-items:flex-end; 
	padding: $pd;
}


