@charset "utf-8";

/**
* Module.Tab
* ------------------
*/


//------------------------
//Swiper Tab

$tab-scroll-margin : 5px; //swiper scroll 간격


.tab-nav{position: relative; margin-top:10px;
	@include sectionFull();
	&:after{content:''; position: absolute;bottom:4px; left: 0;right: 0;height: 1px; background-color: #ddd;}
}

.tab-swiper{
	.swiper-wrapper{flex-wrap:nowrap;justify-content:center;padding-bottom: $tab-scroll-margin; 
		li{text-align: center; width:auto; min-width:75px;
			> a{position: relative;display: block; padding: 30px 50px;  color: #666; text-align: center; text-decoration: none; font-size: $fz-lg; line-height: $fz-lg; background: #fff;
				&:after{@include navbar_barMotion_init();height:calc(#{$tab-scroll-margin + 2px} - 1px);background: $point-color-primary;}//navbar.scss 참조
			}
			> a.active {color: $point-color-primary;
				&:after{@include navbar_barMotion();}
			}
		}
	}
}


@include mediaQuery(pc_under){
	.tab-nav{margin-top:0;}
	.tab-swiper{
		.swiper-wrapper{justify-content:initial;}
	}
}
@include mediaQuery(phone){
	.tab-swiper{
		.swiper-wrapper{
			li {
				a{font-size: $fz-md;line-height: $fz-md; padding: 20px 20px; }
			}
		}
	}
}

.tab-content{margin-top: 55px;
	@include mediaQuery(phone){
		margin-top: 30px;
	}
	.list-head {padding-top:0;}

	& .tab-content{margin-top: 0;}
}


//------------------------
//Essay Tab : DC-MY-010801
.tab-nav-check{
	.tab-wrapper{@extend .btn-group; @extend .btn-group-toggle;
		a{@extend .btn; @extend .btn-outline; min-width:160px;
			span{position: relative; padding-left: 0;}
		}
		a.active{
			span{padding-left: 26px;}
			span:after{content:'';position: absolute;background:url($svg_tab_check) center center no-repeat; left: 0;top: 8px;width: 16px;height: 12px;}
		}
	}
}




//------------------------
//Line Tab
@mixin tab-line(){
	ul{border-bottom:1px solid #666;
		>li{text-align: center; width:50%; }
		>li>a{display:block;position:relative;height:50px;line-height:50px;padding:0 20px;background-color:#fbfbfb;font-size:$fz-md;font-weight:$fw-n;color:$fc-light;text-align:center}
		>li>a:before{content:'';position:absolute;top:0;left:0;right:0;bottom:0;border-width:1px 1px 0;border-style:solid;border-color:#e6e6e6}
		>li+li>a:before{border-width:1px 1px 0 0}
		>li>a.active{background-color:#fff;color:$fc-base}
		>li>a.active:before{left:-1px;right:-1px;bottom:-1px;border-width:1px;border-color:#666 #666 #fff;z-index:1}
		>li>a.active:after{content:'';position:absolute;bottom:-2px;left:0;right:0;height:1px;background-color:#fff}
		>li:first-child>a.active:before{left:0}
		>li:last-child>a.active:before{right:0}
	}
}

//학교찾기 : DC-JA-030502
.tab_line{
	@include tab-line();
}

