.bootstrap-datetimepicker-widget{
	&.dropdown-menu{//width: 100%;
		width: auto; min-width:270px;
		&:after{background: none;}
	}
	table{table-layout:inherit !important;}
	table td,
	table th{height: 22px !important;min-height: 22px !important;width: 22px !important;line-height:22px !important;margin: 0;padding:5px 8px !important;vertical-align: middle;text-align: center;border-bottom: none;}
	table td span{text-align: center;}
}


.input-group.date{@include clearfix;display: block; border:1px solid #ccc;
	.form-control{border:none; float: left; display: inline-block; width: calc(100% - 46px);}
	.input-group-append{ float: left; height: 45px;}

	.datetimepicker-input{border-right-color: #fff; }
	.input-group-text{border:none;display: flex; align-items: center; padding: 0.375rem 0.75rem; margin-bottom: 0; font-size: 1rem; font-weight: 500; line-height: 1.5; color: #495057; text-align: center; white-space: nowrap; background-color: #fff; border-radius: 0; }
	.icon-date{background: url($svg_icon_date) center center no-repeat;width: 20px; height: 18px;}
}

html.pc-only{
	.input-group.date{
		.datetimepicker-input{min-width:235px;width:235px;}
	}
}


.fa-chevron-left{display:inline-block;background: url($svg_date_prev) center center no-repeat; width: 10px; height: 16px; background-size: 10px 16px;}
.fa-chevron-right{display:inline-block;background: url($svg_date_next) center center no-repeat; width: 10px; height: 16px; background-size: 10px 16px;}