@charset "utf-8";

/**
* Module.Accordion
* ------------------
* 자주하는 질문/궁금합니다/공채 온라인상담
*/

.toggle-list-wrap { border-top:2px solid #999; 
	li{
		&.nodata { height: 240px; display: flex; justify-content: center; align-items: center; border-bottom: 1px solid #e4e4e4; }
		dl{ position:relative; overflow:hidden;}
		dl.completed dt a{//답변완료
			.drop{
				.in-drop {
					.state { color: $fc-point; }
				}
			}
		}
		dt{position:relative; font-size: $fz-md; font-weight: $fw-m; border-bottom:1px solid #e4e4e4; 
			a{display:flex; padding:30px 20px; //@include common-transition();
				.num {flex-basis: 70px; font-style:normal; color:#197ddb; font-weight: $fw-m;}
				.tit {flex-grow:1; width:calc(100% - 30px); color: $fc-base; font-weight: $fw-m;}
				.drop { display:flex; width:100%;
					.in-drop { display:flex; flex-basis:295px;
						.state {flex-basis: 120px;}
						.date {flex-basis: 145px;}
					}
				}
				&:hover {color:$fc-base;}
				&:after { content:''; display:inline-block; position:absolute; right:20px; top:calc(50% - 3px); width:10px; height:10px; border-width:2px 2px 0 0; border-color:#333; border-style: solid; transform: rotate(135deg); @include common-transition();}
			}
		}
		dd{display: none;padding:30px 90px;font-size: $fz-md; color:#333; line-height:26px; background:#f6f6f6; border-bottom:1px solid #e4e4e4; 
			.advice {margin-top:25px; padding-top:22px; text-align:center; color:#666; border-top:1px solid #e4e4e4;
				.btn {margin:10px 0 0; font-weight: $fw-m; background:#fff;}
			}
		}
	}
	li.active{
		a:after{content:''; position:absolute; transform: rotate(315deg);}
	}


	@include mediaQuery(pc_under){
		border-top:1px solid #999;
		li{
			dt{
				a{ width:100%; padding:16px;
					.num { margin-top: -1px; flex-basis: 40px; font-weight: $fw-b;}
					.tit {flex-grow:1; line-height:22px;}
					.drop { display:flex; flex-direction:column;
						.in-drop { margin-top:10px; flex-basis:auto;
							.state {position:relative; flex-basis: 78px; color: $fc-base; font-weight: $fw-m; letter-spacing: -1px;
								&:after {content:''; display:inline-block; position:absolute; right:10px; top:50%; transform:translateY(-50%); width:1px; height:14px; background:#ddd;}
							}
							.date {flex-basis: auto; }
						}
					}
				}
			}
			dd{padding:16px 18px;}
		}
	}
}