@charset "utf-8";

/**
* Module.grid
* ------------------
*/


.grid-wrap{@include clearfix();}
	.block {display:inline-block; width: 100%; min-width:100px !important; }
	.block span { vertical-align: top; }
	.half {display:inline-block; width: 50%; min-width:100px !important; }
	.block + .block {margin-top: 10px; }
	.block + .btn , .btn + .block {margin-top: 10px; }


// 개별 그리드 span================================
//span1~12, mo_span1~12
@for $c from 1 through $grid-column{
	.span#{$c}{
		@include clearfix();
		$gutter : $grid-gutter;
		$first : (($c / $grid-column) * 100) - $gutter;

	/*	@if $grid-column == 12 {
			$width : 100%;
			$gutter : 0;
		}*/

		&{
			float:left;
			width: $first !important;
			margin-right: $gutter;
			margin-bottom:$grid-bottom;
		}

		&.last{
			width: $first + $gutter;
		}
	}
}
.span12{width: 100%;margin-right: 0;}
// .last{float:right; margin-right:0;}



@include mediaQuery(phone){
	@for $c from 1 through $grid-column{
		.mo-span#{$c}{
			@include clearfix();
			$gutter : $grid-gutter;
			$first : (($c / $grid-column) * 100) - $gutter;

			&{
				float:left;
				width: $first;
				margin-right: $gutter;
				margin-bottom:$grid-bottom;
			}
			&.mo-last{
				width: $first + $gutter;
			}
		}
	}

	.mo-span12{width: 100%;margin-right: 0;}
	.last{float:left; margin-right:0;}
	.mo-last{float:right; margin-right:0;}
}


//group grid ================================
//정분할
// .grid2, .grid12
@for $n from 1 through $grid-column{
	.grid#{$n}{@include gridGroup($n, $grid-gutter); }
	.grid#{$n}.no-pad{@include gridGroup($n , 0); }
}

@include mediaQuery(phone){
	@for $n from 1 through $grid-column{
		.grid#{$n}, .grid#{$n}.no-pad{
			& > *:nth-of-type(#{$n}n) {float:left;}
		}
	}

	@for $n from 1 through $grid-column{
		.mo-grid#{$n}{@include gridGroup($n , $grid-gutter);}
		.mo-grid#{$n}.no-pad{@include gridGroup($n , 0);}
	}
}

//Devide Grid  ================================
//양쪽 정렬 grid6-6
.grid-both{
	@include clearfix();
	&> :first-child{float:left;}
	&> :last-child{float:right; margin-left:10px;}

	@include mediaQuery(phone){
	}
}

//group grid ================================
//상대 분할
//grid1-11, grid11-1
@for $c from 1 through $grid-column{
	.grid#{$c}-#{$grid-column - $c} {
		@include clearfix();
		$gutter : $grid-gutter;
		$first : (($c / $grid-column) * 100%) - $gutter;
		$second : (100 - $first) - $gutter;

		& > * {

			float:left;
			width: $first !important;
			margin-right: $gutter !important;
			margin-bottom:$grid-bottom;
			min-width:0 !important;
		}

		& > *:last-child {
			float:right;
			width: $second !important;
			margin-right: 0 !important;
			min-width:0 !important;
		}
	}


	.grid#{$c}-#{$grid-column - $c}.no-pad {
		@include clearfix();
		$gutter : 0;
		$first : (($c / $grid-column) * 100%) ;
		$second : (100 - $first);

		& > * {
			float:left;
			width: $first !important;
			margin-right: 0 !important;
			margin-bottom:$grid-bottom;
			min-width:0 !important;
		}

		& > *:last-child {
			float:right;
			width: $second !important;
			margin-right: 0 !important;
			min-width:0 !important;
		}
	}
}


.btn-area.last{
	float:none;
}