@charset "utf-8";

/**
* Module.Table
* ------------------
*/

$table-padding : 16px 15px;
$table-height : 70px;

.table-area{border-top:2px solid #999 ; border-bottom:1px solid #e4e4e4;
	//overflow-x:auto; overflow-y:hidden;
	table{min-width:100%;}
	th, td{min-height: 70px;padding:$table-padding;vertical-align: top;}
	th{
		// width: 20%; // table-data 모바일 사이즈에서 깨짐으로 인한 주석처리 (DC-LO-020200) - 20201228 kjk
		border-bottom:1px solid #e4e4e4 ;color:#000; font-weight:700;}
	td{border-bottom:1px solid #e4e4e4;}

	thead{
		th{text-align:center;}
	}
	> tbody{word-break: break-word;
		> th{text-align: left; vertical-align: top; padding-left: 30px;}
		> td{text-align:left;}
		> tr:last-child{
			th,td{border: none;}
		}
	}
	tfoot{
		th,td{font-weight:bold;color:#ff0000; background-color:#f7f7f7;}
	}

	&.align-center{
		th, td{text-align:center;}
	}

	table{
		.form-group {padding-bottom:0;}
	}


}


//-------------------
// table-mobile
.table-area.table-mobile{
	th, td{min-height: 60px;padding-left: 3px; /*font-size: 14px;*/}

	.inner-table{
		.thead{
			.th{/*font-size: 14px;*/font-weight: 700;color: #000; text-align: left;}
		}
		.tbody{margin-top: 8px; }
		.th{/*font-size: 14px;*/color:#666; font-weight:700; min-height: 25px;}
		.td{padding-left: 5px;min-height: 25px;}
	}

	.inner-table.col2{
		.thead,
		.tbody{
			@include clearfix();
			& > * {float:left; width: 40%; }
			& > *:nth-of-type(2n) {float:right; width: 60%; }
			> *:first-child{float:left !important;}
		}
	}

	.report-input{font-size: 14px; color: #999;}
}



.table.table-hover tbody tr:hover{background-color: #f5f5f5; }

// table col
.table {
	&.col2 colgroup col {width: 50%;}
	&.col3 colgroup col {width: 33.33%;}
	&.col4 colgroup col {width: 25%;}
	&.col5 colgroup col {width: 20%;}
}

//colgroup
.table{
	colgroup{
		.col4-1{width: 170px;}
		.col4-2{width: 430px;}

		.col2-0{width: 240px;}
		.col2-1{width: 170px;}
		.col2-2{width: auto;}
	}
}

//-------------------
// table-responsive
.table-area.table-responsive {margin-bottom: 10px;
	.form-group {margin-bottom: 0;
		&.type-wd {width: 46%;}
	}

	@include mediaQuery(pc_under){
		> table {
			> tbody > tr{
				> th {min-height: 0;display: block; border: 0; padding-left: 15px; padding-bottom: 0;}
				> td {min-height: 0;display: block; padding-top: 0;}
			}
		}
	}
}




//-------------------
.table-col {margin:20px 0;
	thead {
		th {font-weight: $fw-b; border-bottom:0; background:#f6f6f6; }
	}
	tbody {
		td { vertical-align:middle; font-size:16px; }
		.part { width:240px; text-align:center;}
		.period { display:block; margin-top:2px; /*font-size: $fz-xs;*/ color: $fc-light;}
		.tit { width:370px; }
		.state { width:145px; text-align:center;}
		.survey { padding:20px 30px 20px 65px;
			button { margin-left:30px; padding:13px 0;}
		}
	}
}




//-------------------
// 추가버튼 + 테이블
html.pc-only .table-list.table-pc {
	&.sub-wrap {
		.table-list-wrap .table-list-item .table-list-cont select {min-width: 230px;}
		.input-group.date .datetimepicker-input {width: 200px; min-width: 200px;}
		.form-group .form-grade input.ip-grade {width: 220px; min-width: 220px;}
	}
	&.table-type {
		.table-list-wrap .table-list-item .table-list-tit {width: 12%;}
	}
	border-top: 2px solid #999999;
	border-bottom: 1px solid #e4e4e4;
	.table-list-wrap {position: relative; padding-top: 20px;
		&:not(:first-child) {border-top: 1px solid #e4e4e4;}
		.table-list-item {display: table; width: 100%;
			.table-list-tit {display: table-cell; width: 20%; padding: 0 15px 20px 30px; vertical-align: top; padding-top: 10px;
				label {color: #000; font-weight: 700;}
			}
			.table-list-cont {display: table-cell; width: 80%; padding: 0 15px 20px 15px;
				// select {min-width: 260px;}
				button.ico-list-drop {position: absolute; top: 32px; right: 15px; width: 24px; height:24px; background:url($svg_del) 0 0 no-repeat;
					.hidden {position: absolute; top:auto; left:-10000px; width:1px; height:1px; overflow:hidden;}
				}
				button.btn-close {display: inline-block; width: 24px; height: 24px; background:url($svg_ico_drop) 0 0 no-repeat; background-size: 100%; vertical-align: middle; margin-left: 10px;
					span {position: absolute !important; top:auto; left:-10000px; width:1px; height:1px; overflow:hidden;}
				}
				.form-group .input-group-append .input-group-text.ip-disabled {background: #e9ecef;}
			}
		}
	}
	&.table-thesis {
		.table-list-wrap .table-list-item .table-list-cont {
			.form-group {
				input.ip-wd {width: 885px;}
				select {width: 460px;}
				textarea {width: 930px; height: 280px;}
			}
		}
	}
	&.table-leave {
		.input-group.date .datetimepicker-input {min-width: 130px; width: 130px;}
		.table-list-wrap .table-list-item .table-list-cont {
			.form-group {
				input.ip-reason {width: 510px;}
			}
		}
	}
	&.table-level {
		.input-group.date .datetimepicker-input {min-width: 180px; width: 180px;}
		.table-list-wrap {
			.table-list-item {
				.table-list-tit {
					padding: 7px 15px 20px 30px; vertical-align: top;
				}
				.table-list-cont {
					select {width: 208px;}
				}
			}
		}
	}
}

.table-list-wrap {
	&.nodata {min-height:240px; display:flex; align-items:center; justify-content:center; padding-top:0; font-size:16px;}
}

//tbf-base table
.tbf-base{margin-top:20px;border-top:1px solid #999;
	tbody{
		th{background:#f6f6f6;font-weight:800;color:#333;vertical-align:middle;}
		td{padding:20px 30px;}
	}
	.tbf-base{
		tbody{
			th{font-weight:700;}
		}
	}
}

@include mediaQuery(pc_under) {
	.tbf-base{border-top:1px solid #999;
		tbody{
			th{width:20%;}
		}
	}
	.table-area.column-type.inline-type {padding-bottom: 10px;
		table tbody tr th {min-height: 20px;}
		table tbody tr th,
		table tbody tr td {
			display: block;
			padding-left: 0
		}
	}
	.table-area.table-pc.type-wd {
		table tbody tr th {border-bottom: 0; padding-bottom: 0; min-height: 20px;}
		table tbody tr td {padding-top: 0;}
		table tbody tr th,
		table tbody tr td {
			display: block;
			padding-left: 0
		}
	}
}

// 학력테이블 안에 리스트
ul.table-sub-list {
	li {margin-top: 20px;
		&:first-child {margin-top: 0;}
		button.ico-list-add {width: 24px; height:24px; background:url($svg_add) 0 0 no-repeat;
			.hidden {position: absolute; top:auto; left:-10000px; width:1px; height:1px; overflow:hidden;}
		}
		button.ico-list-drop2 {width: 24px; height:24px; background:url($svg_del) 0 0 no-repeat;
			.hidden {position: absolute; top:auto; left:-10000px; width:1px; height:1px; overflow:hidden;}
		}
	}
}

@include mediaQuery(phone){
	.tbf-base tbody th, .tbf-base tbody td{padding:15px 12px;}
	.tbf-base{
		tbody{font-size:14px;
			th{min-width:73px;}
		}
	}
}



