@charset "utf-8";

/**
* Module.Badge
* ------------------
*/

// badge style
$badge-pd01 : 0px 16px;
$badge-pd02 : 0px 10px;
$badge-pd03 : 0px 16px;


// badge 
@mixin badgeStyle {
	display:inline-block;
	height:26px;
	line-height:26px;
	border-radius:26px;
	margin-right:5px;
	@include common-transition();
	// @include mediaQuery(pc_under) {
	// 	height: 24px;
	// }
}

.badge-type {
	@include badgeStyle;
	@include mediaQuery(pc_under) {
		// font-size: $fz-xs;
		background: none;
		font-weight: $fw-m;
	}
	padding: $badge-pd03;
	color: $fc-base;
	font-size: $fz-sm;
	font-weight: $fw-l;
	background: $background-color02;
}

.badge-complete {
	@include badgeStyle;
	// @include mediaQuery(pc_under) {
	// 	font-size: $fz-xs;
	// 	font-weight: $fw-m;
	// }
	padding: $badge-pd02;
	color: $fc-base;
	font-size: $fz-sm;
	font-weight: $fw-m;
	background: $background-color02;
}

.badge-day {
	@include badgeStyle;
	// @include mediaQuery(pc_under) {
	// 	font-size: $fz-xs;
	// }
	padding: $badge-pd01;
	color: $fc-bg-point;
	font-size: $fz-sm;
	font-weight: $fw-m;
	background: $background-color03;
}

