@charset "utf-8";

/**
* Page.login
* ------------------
*/

.login-wrap{max-width:600px;margin:0 auto;
	.login-cont{//padding-top:16px; 
		> .table-list-wrap{padding-top: 16px;}
		.table-area.table-responsive {

		}
	}

	.btn-area.last{justify-content:center;
		& > *{width: 100%;}
	}
}


//---------------------------------------------------
//통합 로그인
.main-panel[data-ui-category="login"]{
	.login-head{border-bottom:2px solid #999;padding-bottom:20px;}

	.login-cont{
		.table-list-wrap{border-bottom:1px solid #eee;margin-bottom:15px;}
		.table-list-item{display:table;width:100%;padding-bottom:6px;
			.table-list-tit{display:table-cell;width:30%;vertical-align: middle;padding-left:30px;font-weight:800;}
			.table-list-cont{display:table-cell;width:70%;vertical-align: middle;padding-right:20px;}
		}
		.find-idpw{text-align: center;
			.btn-link{color:#333;display:inline-block;vertical-align:middle;padding:10px;flex:none;
				&:after{content:'';position: absolute; right: 0;display:inline-block;width:1px;height:16px;background-color:#ddd;vertical-align:middle;}
				&:last-child{margin-left:0;
					&:after{display:none;}
				}
			}
		}
		.btn-login{ @include btn-lg(); width: 100%; }
		.sign-up{display:flex;justify-content:space-between;align-items:center;font-size:16px;border-top:1px solid #eee;padding-top:20px;
			.btn{min-width:160px;}

			@media (max-width: 590px){
				& { display: block; @include clearfix; }
				p{ float: left; }
				.btn{ float: right;width: 100%;margin-top: 20px; }
			}
		}
	}
	@include mediaQuery(phone){
		.login-cont{
			.table-list-item{
				.table-list-tit{display:block;width:100%;padding-left:0;}
				.table-list-cont{display:block;width:100%;padding-right:0;}
			}
		}
	}
}


//---------------------------------------------------
//회원가입
//가입완료
.login-wrap.completed{background:#fcfcfc;padding:60px 50px;text-align:center;margin-bottom:50px;
	&:before{content:'';display:block;width:60px;height:60px;border-radius:50%;background:#31cda9 url($svg_check_large) center/30px 22px no-repeat;margin:0 auto;}
	.title{font-size:34px;margin-top:30px;}
	.sub-title{ margin-top:15px;}
	.completed-foot{margin-top:30px;padding-top:40px;border-top:1px solid #e4e4e4;
		.log-id{color:#999;font-weight:700;margin-right:30px;}
		.log-value{font-weight:800;}
	}
}

//회원가입
.member-item{
	// .h3-title{font-size:24px;}
	.title-wrap{display:flex;justify-content:space-between;align-items:center;
		.list-star{font-size:14px; color:#cc3333;
			&:before{color:#cc3333;}
		}
	}
	+ .member-item{margin-top:60px;}

	.scroll-wrap{width:100%;max-height:200px;overflow-y:scroll;border:1px solid #ddd;box-sizing:border-box;padding:20px 30px;margin-top:20px;
		span{font-size:16px;color:#666;}
		&::-webkit-scrollbar{width:0!important;}
	}
	span.sub-txt {color:#cc3333;}
}


//본인인증 : DC-LO-020100
.tab-certi{
	.tab-header{margin-top: 20px;
		@include tab-line();
		ul{
			li > a {height: 60px;line-height: 60px; /*font-size: 18px;*/}
			li > a > span{position: relative; padding-left: 30px;}
			li:first-child a span:after{content:''; position: absolute;left: 0;top: 2px; background: url($svg_login_phone) center center no-repeat;width: 25px;height: 25px;}
			li:first-child a.active span:after{content:''; position: absolute;left: 0;top: 2px; background: url($svg_login_phone_active) center center no-repeat;width: 25px;height: 25px;}
			li:last-child a span:after{content:''; position: absolute;left: -3px; top: 2px;background: url($svg_login_email) center center no-repeat;width: 25px;height: 25px;}
			li:last-child a.active span:after{content:''; position: absolute;left: -3px; top: 2px;background: url($svg_login_email_active) center center no-repeat;width: 25px;height: 25px;}
		}
	}
	.tab-content{margin-top: 20px;
		@include mediaQuery(phone){
			padding-left: 0; padding-right: 0;
			.btn{font-size:14px;}
		}
		.certi-form{position: relative;
			.time-txt{position: absolute;z-index:10 ;right: 10px; top: 10px;color:#cc3333;}
			@include mediaQuery(phone){
				.form-number{	
					.form-select{
						select{padding:0 30px 0 10px; background-position:top 50% right 12px;}
					}
					.form-group{
						.num-domestic{
							input{padding:11px 5px 11px 11px;}
						} 
					} 
				}
			}
		}
	}
	// .form-number {display: flex; flex-wrap: nowrap;
	// 	span {display: inline-block; padding: 11px 10px;}
	// 	.form-group {flex-grow: 1;}
	// }
}


.tab-pane.fade.show.active {
	.form-group {
		.time-box {position: relative;
			span.time-txt {position: absolute; top: 10px; right: 15px; color: #cc3333;}
		}
	}
}

@include mediaQuery(phone){
	.terms-section{font-size:14px;}
	.completed-foot{
		.log-id{display:block;margin-right:0 !important;}
		.log-value{display:block;}
	}
}