@charset "utf-8";

/**
* Module.dropdown
* ------------------
* 입사지원 조회 (	MO-MY-010602 ~ MO-MY-010606)
*/

.section.section-dropdown{
	.dropdown-btn{padding-left: 0;padding-right: 0;}
}


.dropdown{position: relative;padding-left: 40px;padding-right: 40px; max-width:1280px; margin: 0 auto;
	@include mediaQuery(phone){
		padding-left: 15px;padding-right: 15px;
	}
	&-btn{position: relative;display: block; z-index: 99;background: #fff;padding: 19px 8px;
		span{font-weight: $fw-b; font-size: 18px;}
		&:after{content:''; position: absolute;right: 0; top: 50%; margin-top: -13.5px;width: 15px;height: 27px; background: url($svg_dropdown_arr) center center no-repeat;background-size: 50%;}
	}

	.lookup-menu{@include shadow-middle();
		top:0; left: 0; width: calc(100% - 80px); border:none; border-radius:0; background-color: #fff; margin: 0 auto; padding:20px 15px;
		@include mediaQuery(pc_under){
			width:100%; transform: translate3d(0px, 65px, 0px) !important;
		}
		@include mediaQuery(tablet_under){
			padding-left:0; padding-right: 0;
		}

		&:after{content:''; position: absolute; top: 0;left: 50%;right: 50%; height: 1px; background-color: $point-color-primary; }
		.dropdown-item{position: relative;padding:7px 15px; //font-size: 14px;
			&.active, &:focus{background:none;color: $point-color-primary;}
		}
	}

	&.show{
		.dropdown-btn:after{content:''; position: absolute;width: 15px;height: 27px; background: url($svg_dropdown_arr_on) center center no-repeat;background-size: 50%;}

		.dropdown-menu{z-index: 100;visibility:visible;
			&:after{left: 15px;right: 15px; background-color: $point-color-primary; @include common-transition();}
		}
	}
}

