$navbar-dep1-height : 88px;
$navbar-dep1-height-tablet : 76px;
$navbar-dep1-height-phone : 56px;

$navbar-dep2-height :280px; //240px ;
$navbar-line-color :#eee ;

$zindex-navbarBg :1;
$zindex-navbarGnb :99;

$navbar-locationHeigh : 64px;

