//2Depth 메뉴 hover 라인
@mixin navbar_barMotion_init(){
	content: '';position: absolute;width: 0;height:1px;background: $point-color-primary;left:50%; bottom:0; @include common-transition();
}
@mixin navbar_barMotion(){
	content: '';position: absolute;width:100%;left:0;@include common-transition();
}


//==================================================
#header{height: $navbar-dep1-height;background: #fff;width: 100%;position: fixed;top: 0px;left: 0;z-index: 1001; @include common-transition(top);}
#header.active{top:-$navbar-dep1-height;}
.main-panel{padding-top:$navbar-dep1-height;}

@include mediaQuery(tablet){
	#header{height:$navbar-dep1-height-tablet;}
	#header.active{top: -$navbar-dep1-height-tablet;}
	.main-panel{padding-top:$navbar-dep1-height-tablet;}
}
@include mediaQuery(phone){
	#header{height:$navbar-dep1-height-phone;}
	#header.active{top: -$navbar-dep1-height-phone;}
	.main-panel{padding-top:$navbar-dep1-height-phone ;}
}

.navbar{position:relative; display: flex; flex-wrap: wrap; align-items: center; justify-content: space-between;padding:0;
	border-bottom: 1px solid #eee;
	@include mediaQuery(tablet){
		height:$navbar-dep1-height-tablet ;
	}
	@include mediaQuery(phone){
		height: $navbar-dep1-height-phone;
	}

	.container-fluid{z-index:2; display: flex; flex-wrap: wrap; align-items: center; justify-content: space-between; }

	.logo{position:relative;
		.logo{position:relative;display:inline-block;width:100px; height:48px;
			&:after{position:absolute;content:''; left:0; top:2px; width:102px; height:45px; background:url($svg_logo) 0 center no-repeat; background-size: contain; }
			@include mediaQuery(phone){
				 height:25px;
				&:after{height:25px;}
			}
		}
	}
}



//==================================================
//Gnb
.navbar-gnb {width: calc(100% - 700px);
	.gnb-bg{position: absolute;left: 50%;top: $navbar-dep1-height;z-index: $zindex-navbarBg;width: 100vw; height:0; background: #fff; transform: translate(-50%, 0);border-top: 1px solid #eee;
		@include common-transition();

		@include mediaQuery(tablet){
			top: $navbar-dep1-height-tablet;
		}
	}

	> .nav{position: relative;z-index: $zindex-navbarGnb;display: flex;
		//1Depth
		> li{position: relative; flex:1;  height: $navbar-dep1-height;line-height: $navbar-dep1-height;text-align: center;
			@include mediaQuery(tablet){
				height: $navbar-dep1-height-tablet;line-height: $navbar-dep1-height-tablet;
			}
			&:before{content:''; z-index:10;position: absolute;right: 0;top: 43px; width: 2px;height: 2px;background-color: #8a8a8a;}//dot
			&:after{@include navbar_barMotion_init(); bottom: 0; height: 4px; background-color: #fff;}
			&:last-child{
				&:before{content:'';width: 0;height: 0;}
			}
			> a{position:relative;display:block; font-size:20px; font-weight:$fw-m; @include txt_hover(#333);}
			&:first-child{
				.collapse{border-left:1px solid #eee;}
			}
		}
		> li.over,
		> li.active{
			> a{@include txt_hover($point-color-primary);}
		}

		//2Detpth
		.collapse{position: relative;overflow: hidden;z-index: 5; height: 0; display: block !important; border-right: 1px solid $navbar-line-color;	
			@include common-transition(height);
			> .nav{z-index: 99;padding-top: 20px; padding-bottom: 20px;
				> li{display: block;width: 100%;min-height: 40px;line-height: 40px; 
					&.outlink {
						a{
							&:after{content:''; position:absolute; right:-15px; top:8px; background: url($svg-ico-link) 0 center no-repeat; background-size:10px 10px; width:10px; height:10px;}
						}
					}
				}
				> li a{position: relative;  @include txt_hover(#333);
					span{position: relative;display: inline-block;line-height:1.5;font-size:16px;
						&:before{@include navbar_barMotion_init();}
					}
				}
				> li a:hover{@include txt_hover($point-color-primary);
					span:before{@include navbar_barMotion();}
				}
				> li.active a{@include txt_hover($point-color-primary);
					span:before{@include navbar_barMotion();}
				}
			}
		}
	}
}


//Over
.navbar.over{
	.navbar-gnb{
		.gnb-bg{@include shadow-middle(); height: $navbar-dep2-height;border-top: 1px solid $navbar-line-color;}
		> .nav{
			//1Depth
			> li.over{
				&:after{@include navbar_barMotion(); bottom: 0; background-color: $point-color-primary;}
				.collapse{background: #fbfcff;@include common-transition();}
			}
			//2Depth
			.collapse{ height: $navbar-dep2-height;  @include common-transition(height); }
		}
	}
}


//==================================================
//Util
.navbar-util {z-index: 200; display: flex; flex-wrap: wrap; align-items: center; justify-content: end;}
.navbar-util > .nav{
	li {font-size: 0;
		.btn-login{position: relative;display:block;font-size:16px; line-height:20px; padding-left:26px;
			&:after{content:''; position: absolute; left: 0; top: 0; background: url($svg-ico-user) center center no-repeat; background-size:19px 18px;width: 20px; height: 20px;}
		}
		.name{background: url($svg-ico-user) 0 center no-repeat; background-size:19px 18px;height: 20px;font-size:16px; line-height:20px; padding-left:26px; margin-right:6px;
		}
		.btn-logout{padding-left:6px; padding-right: 6px;font-size: 12px;line-height:22px; border: 1px solid #ccc; vertical-align: top;}

		.my-menu{position: relative;display: inline-block;
			.btn-my{position: relative;display:inline-block;font-size:16px; line-height:20px; padding-left:24px; margin-left: 20px;
				&:after{content:''; position: absolute; left: 0; top: 0; background: url($svg-ico-my) center center no-repeat; background-size:18px 18px;width: 20px; height: 20px;}
			}
			.dropdown-my{margin-top:10px; left: -15px;min-width:120px; padding:7px 0px; border-radius:0; border: 1px solid #e4e4e4;
				&:before{content: ''; display: inline-block; position: absolute; border-left: 8px solid transparent; border-right: 8px solid transparent; border-bottom: 7px solid #e4e4e4; top: -7px; left: 60px; }
				&:after{content: ''; display: inline-block; position: absolute; border-left: 6px solid transparent; border-right: 6px solid transparent; border-bottom: 6px solid white; top: -6px; left: 62px; }
				.dropdown-item{
					color: #666; font-size:$fz-sm; text-align: center;
					&.active, &:focus{background:none;color: $point-color-primary;}
				}
			}
		}
	}
	.btn-top-nav{z-index:999;position: absolute; right: 15px; top:15px; display: none;width:42px;height:44px; background:url($svg_nav_hamburger) center center no-repeat; background-size: 20px 16px;}
}


@include mediaQuery(pc_gutter2) {
	.navbar-gnb{width:calc(100% - 380px);}
}


@include mediaQuery(pc_under){
	.navbar-gnb{display:none;}
	.navbar-util > .nav .btn-top-nav{display: block;}//로그인 버튼
	.navbar-util > .nav li:first-child{display: none;}//로그인 버튼
	.navbar-location{display:none;}  //테블릿에서 안보이게 요청
}
@include mediaQuery(phone){
	.navbar-util > .nav .btn-top-nav{display: block;right:5px; top:6px;}//로그인 버튼
}


//==================================================
// navbar-location
.navbar-location{position: absolute;top: $navbar-dep1-height;left: 50%; right: 0;width: 100vw;overflow: hidden;background: $point-color-primary;
	transform: translate(-50%, 0);

	.location-wrap{position: relative;width: 100%;
		> .location {position: relative; width: 100%; height: 0; display: none; overflow: hidden; @include common-transition(height);}
		> .location.active{display: block;}
		> .location.down{height: $navbar-locationHeigh;}
		> .location > ul {font-size: 0; display: block; margin: 0 auto; text-align: center; vertical-align: top;
			> li {display: inline-block; margin: 0 18px; height: $navbar-locationHeigh; line-height: $navbar-locationHeigh; vertical-align: top }
			> li a{position:relative;display: inline-block;font-size:20px;color:#b4d8f9;@include common-transition();}
			> li a span{position: relative; display: inline-block;}

			> li a span:after{@include navbar_barMotion_init();background: #fff;bottom: 20px; }

			> li.active a{color:#fff;}
			> li.active a span:after{@include navbar_barMotion();}
			> li a:hover{color:#fff;}
			> li a:hover span:after{@include navbar_barMotion();}
		}
	}
}

