@charset "utf-8";

/**
* module.step
* ------------------
*/


// 상단 step
.step-area {margin-bottom:60px;
	.step-list {position:relative; display: flex; width:100%; margin:0 auto;
		li {
			flex:1;text-align:center; color:#999; font-weight:700;
			i {display:block; width:40px; height:40px; margin:0 auto 10px; line-height:40px; font-size:18px; text-align:center; font-style:normal; color:#fff; background:#dcdcdc; border-radius:50%;}
			&.current {color:#117ada;}
			&.current i{position:relative; background:#117ada;
				&:after {content:''; display:inline-block; z-index:-1; position:absolute; left:-8px; top:-8px; width:56px; height:56px; background:#d9eaf9; border-radius:50%;}
			}
			&.end {font-size: $fz-md; font-weight: $fw-m; color:#4f789e;}
			&.end i{background-color:#5e9dd7;}
		}
		&:before {content:''; display:block; z-index:-1; position:absolute;  top:20px; left:50%; width:calc(100% - 25%); height:2px; padding:0 40px; transform:translateX(-50%); background:url($imgpath + 'common/step_bg_dot.png') repeat-x;}
		&:after {content:''; display:block; clear:both;}
	}
}

@include mediaQuery(phone){
	//공통 모바일 (회원가입, 로그인, 마이페이지)
	.step-area{
		.step-list{position: relative;
			li {
				em{display: none;}
			}
			li.current{
				em{display: block;font-size: 14px;}
			}
		}
	}
}


//회원가입
.main-panel[data-ui-category="member"]{
	.step-area{
		.step-list{
			li{
				i{
					&.s1{background:#dcdcdc url($svg_step_s1) center/17px auto no-repeat; }
					&.s2{background:#dcdcdc url($svg_step_s2) center/17px auto no-repeat; }
					&.s3{background:#dcdcdc url($svg_step_s3) center/17px auto no-repeat; }
				}
				&.current i{background-color:#117ada;}
				&.end i{background-color:#5e9dd7;}
			}
			+ .login-cont{margin-top:60px;}
		}
	}
}


//마이페이지 > 전형결과 확인
.main-panel[data-ui-category="my"]{
	.step-area{margin-top:60px;
		.step-list {
			&:before {content:''; width:calc(100% - 20%);}
		}
	}
	.step-area + .pass-result{margin-top: 0;}

	@include mediaQuery(phone){
		.step-area{margin-top: 35px; margin-bottom: 15px;
			.step-list{
				li {flex : auto;
					i{width: 25px;height: 25px;font-size: 14px;line-height: 25px;}
				}
				li.current{
					i{width: 30px;height: 30px;line-height: 30px; margin-top:-4px;
						&:after {content:''; display:inline-block; z-index:-1; position:absolute; left:-5px; top:-5px; width:40px; height:40px; background:#d9eaf9; border-radius:50%;}
					}
				}
				&:before {top:12px;}
			}
		}
	}

}
