@charset "utf-8";

/**
* layout.sidebar
* ------------------
*/


$sidebar-width : 260px;//$sidebar-width; //260 (120 : 240)
$sidebar-left-width :120px ;
$sidebar-headerH : 119px; 


.sidebar { z-index: $zindex-sidebar-mo;position: fixed; display: block; top: 0; right: 0; width: $sidebar-width; height: 100vh; padding-right: 0px; padding-left:0; visibility: visible; overflow-y: visible; border-top: none; text-align: left; box-shadow: none;background: $white;
	transform : translate(100%,0);
	@include common-transition();
	.btn-sidebar-close{z-index:-1; position:fixed; display:none; right:-45px; top:10px; width:28px; height:28px; background:url($svg_nav_close) 0 0 no-repeat;}

	.sidebar-header{position: relative; width: $sidebar-width;height:$sidebar-headerH;margin-top:0; border-bottom:1px solid #ddd; 
		.info-wrap{padding: 24px 20px;
			.name{font-size: $fz-lg; font-weight:$fw-b;}
			.slogan{font-size: $fz-sm;color: #999; line-height:1.3;}
		}
		.btn-wrap{z-index:100; position:absolute;bottom:29px; right:20px;  margin-top:0;
			button.btn{@extend .btn-outline; @extend .btn-xs; @extend .btn-round; margin: 0; min-width:50px !important; padding: 0px 7px !important;
				span{font-size: 11px;line-height:18px;}
			}
		}
	}

	.sidebar-body{z-index: 4; position: relative; height: calc(100vh - 80px); overflow: auto; width: $sidebar-width;
		&:after{content:'';position:absolute; left: 0; top: 0; bottom: 0;width: $sidebar-left-width; background-color: $point-color-primary;} 

		//공통
		.nav{ display: block; position: relative; z-index:10;
			li{position: relative;}
			li a{position:relative; display: block; padding:10px 10px 9px 20px; text-transform: capitalize; color : #333;font-size: $fz-sm; font-weight:$fw-m;}
		}

		> .nav{
			//1Depth
			> li:first-child{margin-top: 10px;}
			> li a {color:#d6e8fa; }
			> li.active > a {color : #333; background-color: #fff;}

			//2Depth
			> li > .collapse {
				z-index: 98; position: absolute;left: $sidebar-left-width; right: 0; top:0;
				li.outlink {
					a{
						span{
							&:before{content:''; position:absolute; right:-14px; top:6px; background: url($svg-ico-link) 0 center no-repeat; background-size:10px 10px; width:10px; height:10px;}
						}
					}
				}
				li a{color:#333; font-weight:$fw-n;background: none;
					padding-bottom: 10px;//2Depth 메뉴 높이 조절
					span{position: relative;
						&:after{@include navbar_barMotion_init();}
					}
				}
				li.active a{color: $point-color-primary;}
				li.active a span:after{@include navbar_barMotion();}
				li a:hover{@include txt_hover($point-color-primary);}
			}

			> li:nth-of-type(1) .collapse{ top : 0px;}
			> li:nth-of-type(2) .collapse{ top : -40px;}
			> li:nth-of-type(3) .collapse{ top : -80px;}
			> li:nth-of-type(4) .collapse{ top : -120px;}
			> li:nth-of-type(5) .collapse{ top : -160px;}
			> li:nth-of-type(6) .collapse{ top : -200px;}
			> li:nth-of-type(7) .collapse{ top : -240px;}
			> li:nth-of-type(8) .collapse{ top : -280px;}
		}
	}
}

//============================================
html.sidebar-toggle-mo{ 
	// overflow:hidden;
	// body{overflow:hidden;}
	.sidebar{@include shadow-big(); transform : translate(0,0); @include common-transition();
		.btn-sidebar-close{position:absolute; display:block;z-index:999; left:-50px; top:20px; width:28px;height:28px; background:url($svg_nav_close) center center no-repeat ; }
	}
}

//============================================
.close-layer,
.sidebar-dimm{position: fixed;z-index:-1; top: 0;bottom:0; left: auto; right: 0;height: 100%; width: 100%;  background: rgba(0, 0, 0, 0.4); content: ""; overflow-x: hidden; opacity: 0;
	&.visible{ z-index: $zindex-dimm; opacity: 1;}
}

