@charset "utf-8";

/**
* base.reset
* ------------------
*/

$reset-font-size : $fz-md; //16px
$reset-font-weight : $fw-n;
$reset-letter-spacing : -1.2px;
$reset-line-height : 1.5;

*{-webkit-overflow-scrolling:touch;}
*,*:before,*:after{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box; outline:0;}
html{-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;-ms-overflow-style:scrollbar;-webkit-tap-highlight-color:rgba(0,0,0,0)}
@-ms-viewport{width:device-width}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,table,th,td,input,select,button{margin:0;padding:0}
fieldset,img{border:0 none;}
dl,ul,ol,menu,li{list-style:none;}
blockquote,p{quotes:none;}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none;}
input,select,textarea,button{font-size:100%;vertical-align:middle;}
button{border:0 none;background-color:transparent;cursor:pointer}
table{table-layout:fixed;border-collapse:collapse;border-spacing:0}
body{-webkit-text-size-adjust:none;overflow-x:hidden;overflow-y:auto;}

input[type='text'],input[type='password'],input[type='submit'],input[type='search'],input[type='date'],input[type='number'],input[type='tel']{-webkit-appearance:none;appearance: none;border-radius:0}
input[type="date"]::-webkit-inner-spin-button, input[type="number"]::-webkit-inner-spin-button, input[type="password"]::-webkit-inner-spin-button, input[type="search"]::-webkit-inner-spin-button, input[type="tel"]::-webkit-inner-spin-button, input[type="text"]::-webkit-inner-spin-button, input.text::-webkit-inner-spin-button, input.eng::-webkit-inner-spin-button {-webkit-appearance: none; }
button,input[type='button'],input[type='submit'],input[type='reset'],input[type='file']{-webkit-appearance:button;border-radius:0}
input[type='search']::-webkit-search-cancel-button{-webkit-appearance:none}
caption{height:0;overflow:hidden;font-size:0;line-height:0;text-indent:-20000px;padding:0}
input,select,textarea,button{font-size:$reset-font-size;line-height:$reset-line-height;color:#333; letter-spacing:$reset-letter-spacing;}
a{color:inherit;text-decoration:none;}
a:active,a:hover{text-decoration:none;} 
address,caption,cite,code,dfn,em,var{font-style:normal;font-weight:$reset-font-weight;};
body{font-family:"Noto Sans","Roboto","Helvetica","Arial",sans-serif,"맑은 고딕" !important;font-size:$reset-font-size;font-weight:$reset-font-weight;line-height:$reset-line-height;color:#333;background-color:#fff;letter-spacing:$reset-letter-spacing;}


