//------------------------------------
// Magic Class
//------------------------------------
// https://docs.emmet.io/cheat-sheet/

	.clearfix{*zoom:1}
	.clearfix:before,.clearfix:after{display:table;clear:both;content:""}
	.blind{position:absolute;display:inline-block;font-size:1px;width:1px;height:1px;padding:0;margin:-1px;visibility:hidden;overflow:hidden;border:0;clip:rect(0, 0, 0, 0)}

	/* Visual Formatting */
	.pos-r {position:relative !important;}
	.pos-a {position:absolute !important;}
	.pos-f {position:fixed !important;}
	.pos-s {position:static !important;}

	.fl-l, .fl {float:left !important}
	.fl-r, .fr {float:right !important}
	.fl-n, .fn {float:none !important;}

	.cl{clear:both !important;}
	.cl-n{clear:none !important;}
	.cl-l{clear:left !important;}
	.cl-r{clear:right !important;}
	.cl-b{clear:both !important;}

	.d-b, .db {display:block !important;}
	.d-n{display:none !important;}
	.d-f{display:flex !important;}
	.d-if{display:inline-flex !important;}
	.d-i{display:inline !important;}
	.d-ib{display:inline-block !important;}
	.d-li{display:list-item !important;}
	.d-tb{display:table !important;}
	.d-itb{display:inline-table !important;}
	.d-tbcl{display:table-column !important;}
	.d-tbr{display:table-row !important;}
	.d-tbc{display:table-cell !important;}

	.v-v{visibility:visible !important;}
	.v-h{visibility:hidden !important;}
	.v-c{visibility:collapse !important;}

	.ov-v{overflow:visible !important;}
	.ov-h{overflow:hidden !important;}
	.ov-s{overflow:scroll !important;}
	.ov-a{overflow:auto !important;}
	.ov-i{overflow:inherit !important;}
	.ovx-s{overflow-x:scroll !important;}
	.ovx-a{overflow-x:auto !important;}
	.ovy-s{overflow-y:scroll !important;}
	.ovy-a{overflow-y:auto !important;}

	.cur-a{cursor:auto !important;}
	.cur-d{cursor:default !important;}
	.cur-ha{cursor:hand !important;}
	.cur-hl{cursor:help !important;}
	.cur-m{cursor:move !important;}
	.cur-p{cursor:pointer !important;}

	/* Box Sizing */
	.bx, .bx-b{box-sizing:border-box !important;}
	.bx-c{box-sizing:content-box !important;}

	.bxs-n{box-shadow:none !important;}

	.w-a{width:auto !important;}
	.h-a{height:auto !important;}

	/* Font */
	.fw-l{font-weight:300 !important;}
	.fw-n{font-weight:500 !important;}
	.fw-m{font-weight:700 !important;}
	.fw-b{font-weight:800 !important;}
	.fw-eb{font-weight:900 !important;}

	.fw100{font-weight:100 !important ;}
	.fw300{font-weight:300 !important ;}
	.fw500{font-weight:500 !important ;}
	.fw700{font-weight:700 !important ;}
	.fw800{font-weight:800 !important ;}
	.fw900{font-weight:900 !important ;}

	.fs-n{font-style:normal !important;}
	.fs-i{font-style:italic !important;}

	.fv-n{font-variant:normal !important;}
	.fv-sc{font-variant:small-caps !important;}

	.ff-s{font-family:serif;}
	.ff-ss{font-family:sans-serif;}
	.ff-m{font-family:monospace;}
	.ff-a{font-family:Arial, "Helvetica Neue", Helvetica, sans-serif;}

	/* Text */
	.va-t{vertical-align:top !important;}
	.va-sup{vertical-align:super !important;}
	.va-tt{vertical-align:text-top !important;}
	.va-m{vertical-align:middle !important;}
	.va-bl{vertical-align:baseline !important;}
	.va-b{vertical-align:bottom !important;}
	.va-tb{vertical-align:text-bottom !important;}
	.va-sub{vertical-align:sub !important;}

	.ta-l{text-align:left !important;}
	.ta-c{text-align:center !important;}
	.ta-r{text-align:right !important;}
	.ta-j{text-align:justify !important;}

	.tov, .tov-e{text-overflow:ellipsis !important;}

	.td-n{text-decoration:none !important;}
	.td-u{text-decoration:underline !important;}
	.td-o{text-decoration:overline !important;}
	.td-l{text-decoration:line-through !important;}

	.ti-{text-indent:-9999px !important;}

	.whs-n{white-space:normal !important;}
	.whs-nw{white-space:nowrap !important;}
	.whs-p{white-space:pre !important;}

	.wob-n{word-break:normal !important;}
	.wob-k{word-break:keep-all !important;}
	.wob-b{word-break:break-all !important;}

	.wow-nm{word-wrap:normal !important;}
	.wow-n{word-wrap:none !important;}
	.wow-b{word-wrap:break-word !important;}

	/* Letter-spacing */
	.ls1{letter-spacing:1px}
	.ls2{letter-spacing:2px}
	.ls-1{letter-spacing:-1px}
	.ls-2{letter-spacing:-2px}

	/* line-height */
	.lh38{line-height:38px!important}
	.lh40{line-height:40px!important}

	/* Background */
	.bg-n{background:none !important;}
	.bgz-a{background-size:auto !important;}
	.bgz-ct{background-size:contain !important;}
	.bgz-co{background-size:cover !important;}

	/* Color */

	/* Table */
	.tbl-a{table-layout:auto !important;}
	.btn-f{table-layout:fixed !important;}

	/* Border */
	.bd-n{border:none !important;}
	.bdt-n{border-top:none !important;}
	.bdb-n{border-bottom:none !important;}
	.bdl-n{border-left:none !important;}
	.bdr-n{border-right:none !important;}

	/* Others */
	.fxd-c{flex-direction:column !important;}
	.fxd-cr{flex-direction:column-reverse !important;}
	.fxd-r{flex-direction:row !important;}
	.fxd-rr{flex-direction:row-reverse !important;}

	.fxw-n{flex-wrap:nowrap !important;}
	.fxw-w{flex-wrap:wrap !important;}
	.fxw-wr{flex-wrap:wrap-reverse !important;}

	.jc-c{justify-content:center !important;}
	.jc-fe{justify-content:flex-end !important;}
	.jc-fs{justify-content:flex-start !important;}
	.jc-sa{justify-content:space-around !important;}
	.jc-sb{justify-content:space-between !important;}

	.op-0{opacity:0 !important;}
	.op-30{opacity:.3 !important;}
	.op-50{opacity:.5 !important;}
	.op-80{opacity:.8 !important;}
	.op-100{opacity:1 !important;}

	.op-ie{filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=100);}
	.op-ms{-ms-filter:'progid:DXImageTransform.Microsoft.Alpha(Opacity=100)';}


//-----------------------------------
// Flex (Bootstarp ~)
//-----------------------------------

	.flex-row{flex-direction:row !important}
	.flex-column{flex-direction:column !important}
	.flex-row-reverse{flex-direction:row-reverse !important}
	.flex-column-reverse{flex-direction:column-reverse !important}
	.flex-wrap{flex-wrap:wrap !important}
	.flex-nowrap{flex-wrap:nowrap !important}
	.flex-wrap-reverse{flex-wrap:wrap-reverse !important}
	.justify-content-start{justify-content:flex-start !important}
	.justify-content-end{justify-content:flex-end !important}
	.justify-content-center{justify-content:center !important}
	.justify-content-between{justify-content:space-between !important}
	.justify-content-around{justify-content:space-around !important}
	.align-items-start{align-items:flex-start !important}
	.align-items-end{align-items:flex-end !important}
	.align-items-center{align-items:center !important}
	.align-items-baseline{align-items:baseline !important}
	.align-items-stretch{align-items:stretch !important}
	.align-content-start{align-content:flex-start !important}
	.align-content-end{align-content:flex-end !important}
	.align-content-center{align-content:center !important}
	.align-content-between{align-content:space-between !important}
	.align-content-around{align-content:space-around !important}
	.align-content-stretch{align-content:stretch !important}
	.align-self-auto{align-self:auto !important}
	.align-self-start{align-self:flex-start !important}
	.align-self-end{align-self:flex-end !important}
	.align-self-center{align-self:center !important}
	.align-self-baseline{align-self:baseline !important}
	.align-self-stretch{align-self:stretch !important}


//-----------------------------------
//Box Sizing
//-----------------------------------

	// fontsize :10	~ 40 , 2++ 
	@for $c from 5 through 20{.fz#{$c *2} {font-size:$c*2px !important} }

	// margin : -50 ~ 100 , 5++ == 
	@for $c from 0 through 10{.m#{$c *5} {margin:$c*5px !important} }//m00
	@for $c from -10 through 20{.ml#{$c *5} {margin-left:$c*5px !important} }//ml00
	@for $c from -10 through 20{.mt#{$c *5} {margin-top:$c*5px !important} }//mt00
	@for $c from -10 through 20{.mr#{$c *5} {margin-right:$c*5px !important} }//mr00
	@for $c from -10 through 20{.mb#{$c *5} {margin-bottom:$c*5px !important} }//mb00

	// padding :  -50 ~ 100 , 5++ ==  
	@for $c from 0 through 10{.p#{$c *5} {padding:$c*5px !important} }//p00
	@for $c from 0 through 20{.pl#{$c *5} {padding-left:$c*5px !important} }//pl00
	@for $c from 0 through 20{.pt#{$c *5} {padding-top:$c*5px !important} }//pt00
	@for $c from 0 through 20{.pr#{$c *5} {padding-right:$c*5px !important} }//pr00
	@for $c from 0 through 20{.pb#{$c *5} {padding-bottom:$c*5px !important} }//pb00

	// width == 
	@for $c from 0 through 20{.w#{$c *5} {width:$c*5px !important} }//w00
	@for $c from 0 through 10{.w#{$c *100} {width:$c*100px !important} }

	.w100p{width: 100% !important}



//-----------------------------------
// Etc
//-----------------------------------
	//heading txt
	.h1-title{font-size: 60px; font-weight:$fw-b;}
	.h2-title{font-size: 20px; font-weight:$fw-b;}
	.h3-title{font-size: 20px; font-weight:$fw-b;}
	.h4-title{font-size: 18px; font-weight:$fw-b;}
	.h5-title{font-size: 16px; font-weight:$fw-b;}




	// validation 체크 
	.valid-text{display:inline-block;color:#3399ff}

	// 말줄임 
	.ellipsis{overflow:hidden;display:block;text-overflow:ellipsis;white-space:nowrap;}
	.ellipsis-init{overflow:initial;display:block;text-overflow:initial;white-space:initial;}

	// 구분 막대 
	.txt-bar{position:absolute;content:"";zoom:1;top:7px;left:0;width:1px;height:11px;background-color:#666}

	// 링크 
	.link-txt{position:relative;}
	.link-txt:before{position:absolute;content:"";zoom:1;left:-3px;right:-3px;bottom:2px;height:1px;background-color:#117ada}

	// 링크 밑줄 
	.link-underline{position:relative; color: #117ada;}
	.link-underline::after{content:'';height:1px;position:absolute;bottom:-2px;left:-3px;right:-3px;background:#117ada;}
