$font-path-roboto : '../font/roboto/';


//--------------
//breakpoint
$breakpoint-mo-under-min : 0;
$breakpoint-mo-under-max : 359px;

$breakpoint-mo-min : 360px;
$breakpoint-mo-max : 766px;

$breakpoint-tablet-min : 767px;
$breakpoint-tablet-max : 1080px;

$breakpoint-pc-min : 1081px;
