///--------------
// Layout
$top-height : 60px; //navbar
$mo-top-height : 50px; //mo navbar
$mo-right-topbar-height : 43px; //mo right topbar

$footer-height:60px;
$sidebar-width : 260px;
$content-width : calc(100% - 260px);
$content-height : calc(100vh - #{$top-height});
$content-title-height : calc(100vh - #{$top-height} - 43px);
$contents-title-filter-height :calc(100vh - #{$top-height} - 43px - 50px);
$mo-content-height : calc(100vh - (#{$mo-top-height} + #{$footer-height})); //100vh - (navbar + footer) 
$mo-left-content-height : calc(100vh - (#{$mo-top-height} + #{$footer-height})); //100vh - (navbar + right-topbar + footer) 

$mo-right-content-height : calc(100vh - (#{$mo-top-height} + #{$mo-right-topbar-height})); //100vh - (navbar + right-topbar) 


