@charset "utf-8";

/**
* layout.content
* ------------------
*/

// 간격 조정 : 

/*.section{
	.section + .section{margin-top: 60px;}
}*/

.content{
	.section:nth-of-type(1){
		margin-top: 60px;
	}

	.section + .section{margin-top: 60px; }

	//MY/입사지원서 조회 (mobile-only)
	.section.section-dropdown{border-bottom:5px solid #f5f5f5; max-width:$layout-max-width; padding-left: 0;padding-right: 0; margin-top: 0 !important; 
		& + .section {margin-top: 30px;}
	}
}

@include mediaQuery(phone){
	.content{
		.section:nth-of-type(1){
			margin-top: 30px;
		}

		.section + .section{margin-top: 30px;}
	}
}


/**
* Area
* ------------------
*/
	//grid-area
	.grid-area{position: relative; @include clearfix();}

	// title-area
	.title-area{position: relative;padding:0 ; @include clearfix(); }
	.title-area + .table-area{margin-top: 20px;}
	@include mediaQuery(phone){
		.title-area + .table-area{margin-top: 20px;}
	}

	//table-area
	.table-area{@include clearfix();}
	.table-area + .table-area{margin-top:20px;}
	.table-area + .title-area{margin-top:60px;}


	// * 블릿 :: p, span
	p.list-star, span.list-star { position:relative; padding-left:8px; font-size: $fz-sm; color: $fc-light;
		i {color: $blue; font-style: normal;}
		&:before { @include icoStar; }
		&.large { font-size: $fz-md;
			&:before { font-size: $fz-md; }
		}
		@include mediaQuery(phone) {display:block;}
	}

	// * 블릿 :: ul
	.list-star {
		li {position:relative; padding-left:11px; margin-top:10px; font-size: $fz-sm; color: $fc-light;
			i {color: $blue; font-style: normal;}
			&:before{ @include icoStar; }
			&:not(:first-child) {margin-top:0;}
		}
		&.large li{ font-size: $fz-md; 
			&:before { font-size: $fz-md;}
		} 
	} 

	.entry-wrap, 
	.table-area,
	.table-list-wrap{
		.list-star li{padding-left: 0;
			&:before{content:'';}
		}
	}

