// 체크박스 폰트
.chk-txt {font-size: 26px; font-weight: 800;}

// 테이블
.table-area.table-pc .form-group {
  margin-bottom: 0;
  .input, select {width: 280px;}
}

// 입사지원하기 - pc
.entry-wrap.table-pc {margin-top: 80px; margin-bottom: 30px;
  &.table-lump {margin-top: 0; margin-bottom: 0; border-bottom: 1px solid #e4e4e4;
	.entry-cont .lb-wrap {border-bottom: 0;}
  }
  .entry-cont {margin-top: 30px; border-top: 2px solid #333; border-collapse: collapse;
	.lb-wrap {display: table; width: 100%; border-bottom: 1px solid #e4e4e4;
	  .lb-title {display: table-cell; padding: 20px 30px; width: 240px; padding-top:12px; vertical-align:text-top; font-size: 18px; color: #333; font-weight: 800;}
	  .inp-wrap {display: table-cell; padding: 20px 30px 20px 0;
		p {margin-top:10px; color:#999; font-size:14px;}
		.row-line {display: block;
		  .inp-item {display: inline-block; margin-right: 10px;
			&:last-child {margin-right: 0;}
			}
			&:last-child {margin-bottom: 0;}
		}
	  }
	  .select-wrap {padding: 20px 30px 20px 0; margin-top: 0;}
	  // .row-line-tbl {display: flex; align-items: center; margin-bottom: 10px;
	  //   .inp-item {display: inline-block; margin-right: 10px;
	  //   &:last-child {margin-right: 0;}
	  //   }
	  //   &:last-child {margin-bottom: 0;}
	  // }
	  &.address {
		.lb-cont{display:flex; flex-wrap:wrap; padding:20px 0;
		  .inp-wrap { padding:0;
			&.last {display:flex; margin-top:10px;
			  .addr1 {width: 505px; margin-right:10px;}
			  .addr2 {width: 410px;}
			}
		  }
		}
	  }
	  &.email {
		.lb-cont {display: flex; align-items: center; margin-bottom: 10px; margin-top: 20px;}
		.inp-wrap {padding: 0; padding-right: 15px;}
		.select-wrap {padding: 0; padding-left: 15px;}
		i {font-style: normal;}
		.list-star {margin-bottom: 20px;}
	  }
	}
  }
}

//주민등록번호
.entry-wrap.resident-wrap {
	.entry-cont {
		&.edit {
			.lb-title {min-width: 125px;
				&:not(label) {display:inline-block; padding-top:0;}
			}
		}
		.lb-wrap {
			.lb-group {
				.form-check.form-check-inline.inline-type {margin:0;}
				.lb-group-inline {margin-right:30px;}
				.form-group {width:250px;}
			}
			.lb-group-inline {
				&.address-wrap {
					/* .form-group {width: 200px;} */
				}
			}
			.lb-group-row {width:50%; display:flex; flex-wrap: nowrap; align-items: center; flex: 1 1 0; -ms-flex: 1 1 0;
				.lb-cont { width:auto; 
					.input-group{width:280px;}
				}
			} 
			&.email {
				.lb-group-inline {margin-right:0;}
				.lb-group .form-group.form-email {margin-bottom: 0;}
			}
		}
	}
}


.entry-wrap.file-list {
	.entry-cont{
		.lb-wrap {
			.lb-title i {display: inline-block; margin-left: 5px; font-style: normal; color: #cc3333; vertical-align: middle;}
			.lb-cont button {margin-right: 30px;}
			.lb-cont .lb-group .form-group.select-wd select {width: 240px; min-width: 240px;}
			.lb-cont .list-star.mt-type li {margin-top: 0;}
		}
	}
}

.table-area.table-pc.type-wd {
	table tr td .form-inline.ico-type button.ico-list-drop2 {
		width: 24px;
		height: 24px;
		background: url("data:image/svg+xml,%3Csvg id='ico_del.svg' xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %23999; %7D .cls-2 %7B fill: %23fff; fill-rule: evenodd; %7D %3C/style%3E%3C/defs%3E%3Ccircle class='cls-1' cx='12' cy='12' r='12'/%3E%3Cpath class='cls-2' d='M1524,723v2h-12v-2h12Z' transform='translate(-1506 -712)'/%3E%3C/svg%3E%0A") 0 0 no-repeat;
	}
	
	table tr td .form-inline.ico-type button.ico-list-drop2 .hidden {
		position: absolute;
		top: auto;
		left: -10000px;
		width: 1px;
		height: 1px;
		overflow: hidden;
	}
}

// 파일선택
.file-txt {display: inline-block; vertical-align: middle;
	.file {float: left; margin-bottom: 5px;}
	.file:after {content: ''; display: block; clear: both;}
	span.txt {display: inline-block; color: #999; font-size: 16px; vertical-align: middle;}
	span.file-name {display: inline-block; font-size: 16px; vertical-align: middle;}
	button.btn-close {display: inline-block; width: 24px; height: 24px; background:url($svg_ico_drop) 0 0 no-repeat; background-size: 100%; vertical-align: middle; margin-left: 10px;
		span {position: absolute !important; top:auto; left:-10000px; width:1px; height:1px; overflow:hidden;}
	}
	&.mt-type {margin-top: 7px;
		button {margin-right: 30px;}
	}
}

// ul 리스트
.ul-list {margin-top: 7px;
  &.star {
	li {position: relative; margin-top: 10px; padding-left: 10px; font-size: 14px; color: #a9a9a9;
	  &:before {content: '*'; display: block; position: absolute; top: 4px; left: 0; color: #a9a9a9; font-size: 14px;}
	  &:first-child {margin-top: 0;}
	}
  }
}

.table-area.column-type.inline-type {
	&.add {
		.lb-cont.address-type .lb-group .lb-group-inline.epost + .lb-group .input-type {width: 280px;}
	}
	.lb-cont {
		&.address-type {width: 100%;
			.lb-group {display:flex; flex-wrap:wrap;
				.lb-group-inline{ flex:0 1 auto; -ms-flex:0 1 auto; display:flex; flex-wrap:wrap; align-items: center;
					&.epost { margin-right:10px;
						& + .lb-group {
							.input-type {width:290px;
								&:not(:first-of-type){margin-left:10px;}
							}
						}
						.input-type {width:195px; margin-right: 10px;}
					}
				}
				.lb-group {display: flex; flex-wrap: wrap; align-items: center;}
			}
		}
	}
	table tbody tr td .form-group.form-type {display: inline-block;}
	.wom-btn {display: inline-block; vertical-align: top;
		button {margin-top: 0;
			&.btn-pen.btn-plus {min-width: 35px; padding: 13px 13px;
				.ic.pen {margin-right: 0;}
			}
		}
	}
}

.entry-wrap.resident-wrap.tbl-type {
	&.add {
		.lb-cont.address-type .lb-group .lb-group-inline.epost + .lb-group .input-type {width: 280px;}
	}
	.entry-cont {border-bottom: 1px solid #e4e4e4; padding-top: 16px;
		.lb-wrap {border-bottom: 0; padding: 0 15px 16px 15px;
			.lb-cont {
				&.address-type {width: 100%;
					.lb-group {display:flex; flex-wrap:wrap;
						.lb-group-inline{ flex:0 1 auto; display:flex; flex-wrap:wrap; align-items: center;
							&.epost { margin-right:10px;
								& + .lb-group {
									.input-type {width:290px;
										&:not(:first-of-type){margin-left:10px;}
									}
								}
								.input-type {width:195px; margin-right: 10px;}
							}
						}
						button {margin: 5px 0;}
						.lb-group {display: flex; flex-wrap: wrap; align-items: center;}
					}
				}
			}
		}
	}
}
	


//-------------------
// 마이페이지
//* 전형결과 확인

//합격 불합격
.pass-result{position: relative;background:#f4f4f4;margin-top:80px;margin-bottom:70px;padding:60px 30px 70px;text-align:center; background: #f3fcff;
	&:before{content:'';display:block;width:120px;height:120px;background:url($svg_pass) center center no-repeat;margin:0 auto;}
	.sub-title{font-size: 40px;margin-top:20px;letter-spacing:-1; }
	.title{margin-top: 0px;font-size:40px; letter-spacing:-1;line-height:1.2;
		span {color: #117ada; font-weight:$fw-b;}
	}
	.info-title{font-size: 18px;color: #333;margin-top: 20px;}
	.btn-area{margin-top: 20px;
		button {background: #fff;}
	}
	&.fail {background: #f6f6f6;
		.title{
			span {color: #333;}
		}
	}
	&.fail:before{
		background:url($svg_fail) center center no-repeat;
	}

	@include mediaQuery(phone){
		.sub-title{font-size: 20px;}
		.title{font-size: 20px;}
		.info-title{font-size: 16px;}
		.btn-area{ display: block;
			> .btn{margin:0; width:100%;	
				&:last-child{margin-top:10px;}
				&:first-child{margin-top:0px;}
			}
		}
	}
}

.pass-form{position:relative; min-height:130px; margin-top: 10px; padding: 30px 30px; border: 1px solid #ccc; }

.error-wrap {background-color: #fcfcfc; padding: 60px 0;
	p {text-align: center;
		&.title {font-size: 24px; margin-top: 25px;
			.color-type {font-weight: 800; color: #cc3333;}
		}
		&.sub-title {margin-top: 15px; color: #6f6f6f;}
	}
	&::before {content: ''; display: block; width: 62px; height: 62px; margin-bottom: 25px; background: url($svg_ico_error_page) center center no-repeat; margin: 0 auto;}
	.btn-area {margin-top: 35px;}
	.btn.btn-main {padding: 19px 20px; font-size: 18px;}
}

.popup-wrap {padding-bottom: 30px;
	.entry-wrap {
		&.grade-box {
			// 익스에서 깨져서 달력부분 아래처럼 처리
			.input-group.date .input-group-append {position: absolute; right: 0; height: 46px;}
		}
		margin-top: 0;
		.entry-cont {border-top: 0;
			.lb-wrap {padding: 0; display: flex; flex-wrap: nowrap; border-bottom: 0;
				.lb-cont {margin-right: 10px;
					&:last-child{margin-right:0;}
					input {min-width: 205px;}
					input.form-control.datetimepicker-input {min-width: 160px;}
					.input-group.date .datetimepicker-input {width: 160px;}
				}
			}
		}
	}
	.btn-box {text-align: center;
		button {margin-top: 20px;
			span {color: #117ada;}
		}
	}

	table.grade-box {margin-top: 40px;
		// tr th {font-weight: 800; padding-bottom: 7px;}
		tr td label {font-weight: 800;}
		tr td input {width: 180px; min-width: 180px;}
		tr td .form-group select.width-sm-b {width: 180px;}
	}
}

.empty-box {border-top: 2px solid #999999; min-height: 150px; border-bottom: 1px solid #e4e4e4;
	p {text-align: center; line-height: 150px;}
}

.form-check.form-check-inline {
	label span.chk-font {display:inline-block; margin-top:3px; font-size: 20px; font-weight: 800;
	}
}
.form-check.form-check-inline.m-top label span.chk-font {margin-top: 0;}

.toggle-list-wrap.toggle-type {
	li dd {background: none; padding: 30px 30px; border-left: 1px solid #e4e4e4; border-right: 1px solid #e4e4e4;}
	// li dt a {padding: 30px 0;}
	.tg-tit-item {display:flex; align-items: center; justify-content: space-between;
		.form-check-inline {margin:25px 0; font-weight:$fw-n;}
		a {width:60px; height:40px; padding:0;}
	}
	li .tg-cont-item {overflow-y:scroll; width:100%; max-height:200px; padding:20px 30px;
		span{font-size:16px;color:#666;}
		// &::-webkit-scrollbar{width:0!important;}
	}
	&.size-wrap {border-top: 0; margin-top: 0;
		.tg-tit-item {
			a {position:relative; padding:0; width:auto; height:auto;}
			span:not(.link-underline) {display:inline-block; padding:20px 0;}
		}
		li .tg-cont-item {padding: 30px 30px; max-height:100%; overflow:hidden;} //  min-height: 530px;
		li dt a:after {border-color: #117ada; right:5px; width: 9px; height: 9px; top:7px;}
		li.active dt a:after {top:10px; transform: rotate(315deg);}
		li dt a .size {position: relative;}
		li dt a .size span:after {top:11px;}
		.tg-tit-item a span.link-underline {margin-right: 25px;}
		.tg-cont-item {
			.scroll-wrap {
			 .tbl-size {width: 100%; text-align: center; border-top: 2px solid #999; border-bottom: 1px solid #e4e4e4; border-collapse: collapse;
					th {background: #f6f6f6; font-weight: 800;}
					th, td {padding: 5px 0; border-right: 1px solid #e4e4e4;}
					tr td.line, tr th.line {border-right: 1px solid #e4e4e4;
						&.font-b {font-weight: 800;}
					}
					tr th.line.col {color: #999;}
					tr.border-b {border-bottom: 1px solid #e4e4e4;}
					thead tr th span {color: #000; display: inline-block;
						&:first-child {margin-right: 40px;}
					}
				}
			}
		}
	}
}

.text-type {margin-top: 30px; font-weight:700;
	span.color-b {color: #117ada;}
}
.text-sub-type {font-size:14px; font-weight:500; margin-top:5px; color:#666;}

.tbl-wrap {margin-top: 30px; padding:10px 70px; line-height:1.7; border: 1px solid #e4e4e4;
	a {display:inline-block; font-size:14px; padding:4px 19px 3px; margin-left:10px; font-weight:700; color:#117ada; border:1px solid #117ada;}
	/*.tbl-info {width: 100%; 
		th {width: 20%; vertical-align: top; font-weight: 700; padding: 25px 0 25px 25px;}
		td {width: 80%; color: #333; padding: 15px 25px 25px 25px;}
		span.color-b {color: #117ada;}
		th.pd0, td.pd0 {padding-bottom: 0;}
	}*/
}

.print-wrap { margin-top:10px;
	.btn {display:inline-block;}
}

.attach-file-list {margin: 15px 0 5px 0;
	ul {display: inline-block; vertical-align: text-top;
		li {float: left; position: relative; padding-left: 10px; padding-right: 7px;
			&:first-child {padding-left: 0;}
			&:not(:first-child) {
				&:after {content: '/'; position: absolute; top: 0; left: 0;}
			}
		}
		&:after {content: ''; display: block; clear: both;}
	}
}

.file-wrap {margin-top: 30px; padding:25px; border: 1px solid #e4e4e4;}

.list-star.form-txt {margin-bottom: 60px; 
	li {color: #333;
		&.star-type1::before {color: #333;}
		&.star-type2 {color: #cc3333;
			&::before {color: #cc3333;}
		}
	}
}

// 반응형
@include mediaQuery(pc_under) {
	
	.entry-wrap .entry-cont .lb-wrap .lb-cont .lb-group {
		.form-check {margin: 10px 0 0 0;}
		.form-check-inline {margin-left: 0;}
	}
	.pass-form label {
		span {display: inline-block; font-size: 14px;}
		a {display: inline-block; font-size: 14px;}
	}
	.table-area.column-type.inline-type td {padding-right: 0;}

	.entry-wrap .entry-cont .lb-wrap .lb-cont.size-wrap .form-inline .form-check-inline {display: block; margin-bottom: 10px;}

	//주민등록번호
	.entry-wrap.resident-wrap {
		.entry-cont {
			&.edit {
				.lb-title {min-width: 125px;
					&:not(label) {display:inline-block; padding-top:12px;}
				}
			}
			.lb-wrap {
				.lb-group {display: flex; width: 100%; flex-direction: column; /* flex-wrap: nowrap; */
					.lb-group-inline {width:100%; flex-wrap: nowrap; margin-right:0; }
					.form-group {width:calc(50% - 12px);  margin-bottom: 0;
						&.width-type {width: 100%;}
					}  /* width:auto; flex-grow: 1; */
					span {display: inline-block; padding: 11px 10px;}
				}
				.lb-group-row {flex-direction: column; align-items: flex-start; width:100%; display:block;
					.lb-cont {width:100%;
						.input-group {width:100%;}
					}
				} 
				&.email {
					.lb-group-inline span.text-input-join {margin-top: -17px;}
					.lb-group-inline .form-group.email {flex-grow: 1; align-items: center; margin-bottom: 10px;}
				
					.lb-group .form-group.form-email {width: 100%;}
				}
			}
		}
	}

	.pass-result {padding: 60px 50px 70px;}
	.pass-result .title {/*color: #333; font-weight: 800;*/
		// span {color: #333;}
		margin-top: 20px;
		font-size: 29px;
		span.type-block {display: block;}
	}
	.pass-result .sub-title {
		span.type-block {display: block;}
	}

	
	.table-area.column-type.inline-type {
		&.add {
			.lb-cont.address-type .lb-group .lb-group-inline.epost + .lb-group .input-type {width: 100%;}
		}
		.lb-cont {
			&.address-type {
				.lb-group {flex-direction: column;
					.lb-group-inline.epost {margin-right: 0; margin-bottom: 5px;}
					// .lb-group-inline.epost + .lb-group {width: 100%;}
					.lb-group-inline.epost .btn-srch {min-width: 135px;}
					.lb-group-inline.epost .input-type {width: 195px; flex: 1;
						input {width: 100%;}
					}
					.lb-group-inline.epost + .lb-group {
						.input-type {width:100%; margin-bottom: 10px;
							&:not(:first-of-type){margin-left:0;}
						}
					}
				}
			}
		}
	}

	
	.entry-wrap.resident-wrap.tbl-type {
		&.add {
			.lb-cont.address-type .lb-group .lb-group-inline.epost + .lb-group .input-type {width: 100%;}
		}
		.entry-cont {
			.lb-wrap {border-bottom: 0; padding: 0 0 16px 0;
				.lb-cont {
					&.address-type {
						.lb-group {flex-direction: column;
							.lb-group-inline.epost {margin-right: 0; margin-bottom: 5px;}
							// .lb-group-inline.epost + .lb-group {width: 100%;}
							.lb-group-inline.epost .btn-srch {min-width: 135px;}
							.lb-group-inline.epost .input-type {width: 195px; flex: 1;
								input {width: 100%;}
							}
							.lb-group-inline.epost + .lb-group {
								.input-type {width:100%; margin-bottom: 10px;
									&:not(:first-of-type){margin-left:0;}
								}
							}
						}
					}
				}
			}
		}
	}


	.entry-wrap.file-list {
		.entry-cont{
			.lb-wrap {
				.lb-cont .btn-srch {min-width:133px; padding:13px 0;}
				.lb-cont .file-txt {
					// .file {margin-top: 5px;}
					.file:first-child {margin-top: 0;}
				}
			}
		}
	}

	.toggle-list-wrap.toggle-type {
		// li dt a {font-size: 14px;}
		&.size-wrap {
			li {
				.tg-cont-item { padding:10px;
					.scroll-wrap {
					.tbl-size {font-size: 14px;}						
					}
				}
			}
		}
	}

	.popup-wrap {
		table.grade-box {
			tr td {display: block;}
			tr td input.ip-grade {width: 100%; min-width: 100%;}
			tr td select {width: 100%; min-width: 100%;}
		}

		.entry-wrap {
			&.grade-box {
				.input-group.date .input-group-append {position: absolute; right: 0; height: 45px;}
			}
		}
	}

	.error-wrap {
		.btn.btn-main {padding: 19px 56px; flex: none;}
	}

}
@include mediaQuery(tablet) {
}
@include mediaQuery(phone) {
	.entry-wrap.resident-wrap .entry-cont .lb-wrap {
		.lb-group {
			.form-check.form-check-inline.inline-type {margin-left: 0;}
		}
	}
	.tbl-wrap {padding:10px 35px;
		.mini-btn-wrap {display: block;
			button {margin-top: 5px;}
		}
	}

	.toggle-list-wrap.toggle-type {
		&.size-wrap {
			li dt a:after {top:5px;}
			li.active dt a:after {top:9px;}
			.tg-tit-item a span.link-underline {font-size: 14px;}
		}
	}

	.error-wrap {
		p {
			&.title {
				.color-type {
					.line-br {display: block;}
				}
			}
		}
		.btn.btn-main {padding: 19px 56px; flex: none;}
	}
}
