@charset "utf-8";

/**
* layout.footer
* ------------------
*/

$footer-nav-margin : 120px;

.footer{border-top:1px solid #eee; 
	.footer-wrap{display: flex;  align-items:center;justify-content: space-between;position:relative;margin-right:0;padding-left:$footer-nav-margin; 

	}
	.footer-logo{position:absolute; left:0;top:38px;width:$footer-nav-margin;height:45px;background:url($svg_logo) 0 0 no-repeat; background-size: contain;
		span{@include sr-only;}
	}

	.info-copy{color:#666; font-size:14px; font-weight: $fw-l;letter-spacing:0;
		br{display: none;}
		html.ie &{position: absolute; left: 120px; top: 53px; } // IE10 patch
	}

	.nav{
		li{position:relative; padding-left:20px; padding-right:20px;
			&:before{position:absolute; content:''; top:5px; left:0; width:1px; height:16px; background-color:#999; }
			&:first-child{padding-left:0;}
			&:last-child{padding-right:0;}
			&:first-child:before{content:'';width:0;height:0;}
			a{font-size: 14px;
				&:hover{@include txt_hover($point-color-primary); }
			}
		}
	}

	@include mediaQuery(pc_under){ 
		.info-copy{letter-spacing:-0.8px;}
		.nav li{padding-left:10px;  padding-right: 10px;}
	}

	@include mediaQuery(phone){ 
		background-color: #eee;
		.footer-wrap{justify-content: flex-end;align-items:flex-start;padding-top:20px;}
		.footer-logo{width:60px;height:25px;top:18px;}
		.info-copy{font-size: 12px;text-align: right;letter-spacing:-0.8px;
			br{display:block;}
			html.ie &{position: absolute; right: 15px; top: 20px; } // IE10 patch
		}
		.nav{position: absolute;left: 0;bottom: 20px;
			li a{font-size: 12px;}
		}
	}
}

