$title-margin-bottom: 20px; 

.pc-blind{display:none;}
.pc-show{display:block;}
.mo-show{display:none;}
.hidden{display:none;}
.txt-deco{border-bottom:1px solid #333;margin:10px 0 0 10px;;font-weight:$fw-b;font-size:16px;}
// color 값
.fc-red{color:$red;}
.fc-primary{color:$blue;}
.bul-note{position:relative;padding-left:20px;
  span:before{content:'※';display:inline-block;position:absolute;left:0;}
}
//dot list
.list-dot li{position:relative;padding-left:10px;
  &:before{content:'·';display:inline-block;position:absolute;left:0;}
}
// dash list 
.list-dash li{position:relative;padding-left:10px;
  &:before{content:'-';display:inline-block;position:absolute;left:0;}
}
.list-star{
  &.xlarge{
    li{font-size:18px;color:#333;padding-left:10px;margin-top:0;}
  }
  &.red{
    li:before{color:#cc3333;font-size:$fz-lg;}
  }
}
//테이블 내 별 리스트
.tb-list-star{padding-left:11px;position:relative;
  li:before{content:'*';display:inline-block;position:absolute;top:3px;left:0;}
  &.blue{
    li:before{color:#117ada;}
  }
}


// 마이페이지 - 에세이등록
.e-text-wrap{display:block;border:1px solid #e4e4e4; border-top:2px solid #999;
  &.view{
    .title-wrap{
      .title{
        .tit{padding-right:0;}
      }
    }
  }
  .title-wrap{background:#f6f6f6;padding:20px 30px;
    .title{position:relative;font-weight:$fw-b;
      .tit{display:block;padding-right:90px;}
      .limit{position:absolute;min-width:90px;top:0;right:0;text-align:right;}
      .limit{width:120px;}
      .byte{font-weight:$fw-m;
       em{margin-left:8px;}
      }
    }
    .sub-title{color:#999;font-weight:500;padding-top:8px;}
  }
  .cont{padding:30px;position:relative;
    .form-control{border:none;padding:0px;}
  }
}
.complete-result{padding:45px 20px 40px;background:#f3fcff;text-align:center;
  &:before{content:'';display:block;width:112px;height:115px;background:url($svg_complete_e)no-repeat center center;margin:0 auto;}
  .title{font-size:32px;margin:25px 0 20px;
    strong{font-weight:$fw-b;color:#117ada;}
  }
  .btn-area{margin-top:30px;}
}

// 마이페이지 - 입사지원서 작성
.view-list-wrap{overflow:hidden;width:100%;border-top:2px solid #999;margin-top:$title-margin-bottom;padding-top:30px;
  >ul{position:relative;margin-top:10px;font-size:14px;color:#999;}
}

//반응형 테이블
.tb-list{border-top:1px solid #e4e4e4;
  > li{border-bottom:1px solid #e4e4e4;}
}
.tb-con-res{width:100%;
  > li{float:left;
    &:first-child dl{border-left:1px solid #e4e4e4;}
    dl{border-right:1px solid #e4e4e4;
      dt{text-align:center;padding:28px 0;border-bottom:1px solid #e4e4e4;}
      dd{text-align:center;padding:10px;
        ul li{float:none;}
      }
    }
    &.tb1{width:20%;
      dl{width:100%;
        dd{float:left;width:50%;
          // &:last-child{border-left:1px solid #e4e4e4;}
        }
        &:after{content:'';display:block;clear:both;}
      }
    }
    &.tb2{width:7%}
    &.tb3{width:14%}
    &.tb4{width:44%;
      dd{text-align:left;}
    }
    &.tb5{width:15%}
  }
  &:after{content:'';display:block;clear:both;}
  &.change{
    li{
      dl{
        dt{display:none;}
      }
    }
  }
  &.none-dv{
    > li.tb1 dl dd{width:100%;}
  }
  &.none-person{
    > li.tb4{width:51%;}
  }
}

// border-radius="16px" 태그 모양
.tag{display:inline-block;vertical-align:middle;padding:0px 16px;border-radius:26px;font-size:$fz-sm;line-height:26px; font-weight:$fw-m;margin-right:5px;
  //1/12 : h26 변경
  &.blue{background:#eaf8ff;color:#117ada;}
  &.green{background:#dbf9f2;color:#1d9479;}
  &:last-child{margin-right:0;}
}

// 이전글,다음글
.move-wrap{display:block;border-width:10px 0 10px 0;border-style:solid;border-color:#f5f5f5;margin-top:$title-margin-bottom;padding:0 30px;
  li{display:block;padding:30px 0;border-bottom:1px solid #e4e4e4;
    &:last-child{border:0;}
    span{display:inline-block;font-weight:$fw-b;padding:0 30px 0 8px;width:15%;
      &:before{content:'';display:inline-block;width:0;height:0;vertical-align:middle;margin-right:10px;border-right: 6px solid transparent;border-left: 6px solid  transparent;}
    }
    &.prev span:before{border-top:0;border-bottom:8px solid #333;margin-bottom:5px;}
    &.next span:before{border-top:8px solid #333;border-bottom:0;}
  } 
}

// email-form
.email-form{display:table;width:100%;margin-bottom:10px;
  .form-group{display:table-cell;vertical-align:bottom;
    &.form-email{width:40%;}
  }
  .email-input-join{display:table-cell;vertical-align:bottom;line-height:48px;text-align:center;padding:0 5px;
   
  }
}

//입사지원서
.sub-bul{color:#999;margin-top:5px;
  &:before{content:'*';display:inline-block;position:relative;top:4px;left:0;margin-right:3px;}
}
.apply-item{position:relative;
  &:before{content:'';display:block;position: absolute;
    top: 155px;
    left: 75px;
    background-image: linear-gradient(#eee 33%, rgba(255,255,255,0) 0%);
    background-position: left;
    background-size: 2px 5px;
    background-repeat: repeat-y;
    height: calc(100% - 161px);
    width: 2px;}
    &:last-child:before{display:none;}
  .apply-step{position:absolute;left:0;top:0;width:140px;height:140px;text-align:center;border-radius:50%;background:#f6f6f6;display: flex;flex-direction: column;justify-content: center;align-items: center;margin:6px;
    .p1{font-size:16px;color:#117ada;font-weight:$fw-b;}
    .p2{font-size:24px;font-weight:$fw-m;}
    .p3{font-size:14px;color:#666;}
    .pie{position:absolute;top:-10px;left:-10px;width:160px;height:160px;clip:rect(0,160px,160px,80px);
      .half-circle{height: 100%;width: 100%;border: 4px solid #3498db;border-radius: 50%;left: 0;position: absolute;top: 0;clip:rect(0,80px,160px,0);}
    }
    
    &.percent-72 .left-side{transform: rotate(72deg);}
    &.percent-90 .left-side{transform: rotate(90deg);}
    &.percent-144 .left-side{transform: rotate(144deg);}
    &.percent-180 .left-side{transform: rotate(180deg);}
    &.percent-216 .pie{clip:rect(auto,auto,auto,auto);
      .right-side{transform: rotate(216deg);}
      .left-side{transform: rotate(180deg);}
    } 
    &.percent-270 .pie{clip:rect(auto,auto,auto,auto);
      .right-side{transform: rotate(270deg);}
      .left-side{transform: rotate(180deg);}
    } 
    &.percent-288 .pie{clip:rect(auto,auto,auto,auto);
      .right-side{transform: rotate(288deg);}
      .left-side{transform: rotate(180deg);}
    } 
    &.percent-360 .pie{clip:rect(auto,auto,auto,auto);
      .half-circle{clip:rect(auto,auto,auto,auto);}
    } 
  }
  .apply-cont{display:block;padding-left:198px;padding-bottom:80px;
    li{display:block;}
    .item-head{border-bottom:1px solid #e4e4e4;padding-bottom:20px;}
    .item-cont{padding-top:40px;
      .h3-title{margin-bottom:20px;}
      .list-dot{font-size:16px;}
      &.type-row{display:flex;flex-direction:row;justify-content:flex-start;align-items:flex-start;
        .h3-title,.list-dot:first-child{margin-top:52px}
        .list-dot{margin-bottom:10px;}
        ul + ul {margin-top:102px;}
        a + ul{margin-top:40px;}
        .circle-wrap{position:relative;display:flex;flex-direction:column;padding:0 74px 0 130px;
          span.c{display:flex;align-items:center;justify-content:center;width:130px;height:130px;text-align:center;border-radius:50%;background:rgba(17,122,218,0.8);color:#fff;
            +span.c{position:relative;top:-15px;}
          }
          span.g{background:rgba(49,205,169,0.8);}
          span.g1{border:2px solid #31cda9;background:transparent;color:#31cda9;}
          &:before{content:'';display:block;position:absolute;top:60px;left:0;width:130px;height:100%;background:url($svg_arrow_left) no-repeat;margin-left:8px;}
          &:after{content:'';display:block;position:absolute;top:60px;left:260px;width:74px;height:100%;background:url($svg_arrow_right) no-repeat;margin-left:8px;}
        }
      }
      .intern-wrap{max-width:660px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;margin-top:40px;
        .intern-circle{display:flex;align-items:center;justify-content:center;flex-direction:row;width:130px;height:130px;border-radius:50%;background:rgba(233,233,233,1);text-align:center;padding:0 27px;font-weight:$fw-m;}
        .intern1{display:flex;flex-direction:row;
          .intern-circle:last-child{position:relative;left:-14px;background:rgba(246,246,246,.6);}
        }
        .intern2{position:relative;display:block;border-bottom:1px solid #e4e4e4;padding-bottom:10px;width:100%;text-align:center;margin:0 20px;
          &:after{content:'';display:inline-block;position:absolute;bottom:-8px;right:-1px;width:0;height:0;border-top:7px solid transparent;border-bottom:7px solid transparent;border-left:7px solid #e4e4e4;}
        }
        .intern3{
          .intern-circle{background:rgba(231,241,251,1);color:#117ada;}
        }
      }
    }
  }
}

