//내일을준비하는 - 생생 두산 입사기, 두산인의 하루 (.today)
/**/
.sort{padding:10px 60px 60px;text-align:center;
  &.today{padding:30px 60px 30px;border:1px solid #e4e4e4;background:#fafafa;} // 두산인의 하루
  ul{display:block;max-width:840px;margin:0 auto;}
  li{display:inline-block;padding:5px 4px;}
  button{height:36px;line-height:35px;padding:0 20px;background-color:#fff;border-radius:48px;border:1px solid #dfdfdf;box-sizing:border-box;}
  li.active button{font-weight:$fw-b;background-color:#eaf8ff;color:$point-color-primary;border-color:#519ee4;}
}
.sort_swipe_wrap{position:relative;margin-top:30px;
  .sort-swipe-head{position:relative;padding:0 40px;height:60px;line-height:60px;background:#117ada;font-size:$fz-lg;color:#fff;font-weight:$fw-b;
    .info_txt{display:inline-block;font-size:$fz-md;font-weight:$fw-n;margin-left:10px;
      &:before{content:'·';display:inline-block;color:#fff;margin-right:8px;}
    }
  }
  .motion{opacity:0;transform:translateY(120px);transition:1s;
		&.animated{opacity:1;transform:translateY(0)}
  }
  .control{width:100%;position:absolute;top:50%;right:0;bottom:0;transform:translateY(-50%);z-index:5;height:59px;
    button{display:inline-block;width:60px;height:59px;background:rgba(0, 0, 0, 0.37);
      &.swiper-button-disabled{opacity:0.1;cursor:auto;pointer-events:none;}
      &.today_next{position:absolute;right:0;}
      span{position:absolute;top:40%;width:15px;height:15px;border-width:2px 2px 0 0;border-style:solid;border-color:#fff;font-size:0;}
      &.today_prev span{left:24px;transform:rotate(-135deg);}
      &.today_next span{right:24px;transform:rotate(45deg);}
    }
  }
  .today_swipe{position:relative;overflow:hidden;}
  .sort-swipe-cont{overflow:hidden;margin:0 auto;}
}
// 두산인의 하루
.today{ 
  .sort-swipe-cont{padding:50px 60px;border:1px solid #e4e4e4;border-top:0;}
  .ds_profile{flex: 1 1 auto; -ms-flex: 1 1 auto;flex-direction:row;justify-content:space-between;align-items:flex-start;
    .img{display:block;min-width:140px;width:140px;height:140px;background:#e4e4e4 url($imgpath + 'svg/no-img.png')no-repeat center center;
      &::before{display:none;}
    }
    .info{position:relative;max-width:900px;padding:35px;color:$fc-base;line-height:24px;background:#f6f6f6;margin:0 0 0 40px;border-radius:35px;
      &:before{
        content:""; 
        position:absolute;
        top:0;
        left:-23px; 
        border-top:25px solid #f6f6f6; 
        border-left: 20px solid transparent; 
        border-right: 30px solid  #f6f6f6; 
        border-bottom: 50px solid transparent;
      }
      .h4-title{font-weight:$fw-b;color:$fc-base;margin-bottom:10px;
        &:before{content:'';display:inline-block;width:20px;height:20px;background:url($svg_message)no-repeat center center;margin:0 9px 2px 0;vertical-align:middle;}
      }      
    }
  }
  .step-wrap{width:100%;margin:50px auto 0 auto;
    ul{margin-top:50px;padding-top:40px;border-top:1px solid #e4e4e4;
      li{margin-bottom:35px;color:#666;
