  }

  //두산에대하여 - 공지사항
  .ad-noti-head{position:relative;flex-direction:column;padding:35px 0 25px;border-top:0;border-bottom:2px solid #999;
    .sub-txt{
      .part{position:absolute;top:0;left:0;font-weight:$fw-m;}
      .date{position:relative;top:8px;left:0;}
    }
  }
  .ad-noti-cont{padding:60px 0;}

  //이전글 다음글(모바일일때만 양옆 꽉차게)
  .move-wrap{margin-left:-15px;margin-right:-15px;}

  //내일을준비하는 - 생생 두산 입사기, 두산인의 하루
  /**/
  .sort{display:block;/*overflow-x:auto;*/padding:0 15px;border:0;background:#fff;text-align:left;
    ul{display:block;max-width:none;margin:0;/*white-space:nowrap;overflow:auto;width:auto;*/}
    li{overflow:hidden;padding:0 8px 5px 0}
  }
  .sort_swipe_wrap{
    .sort-swipe-head{height:auto;line-height:28px;padding:18px 20px;
      .info_txt{margin:0 5px 0 0;
        &::before{margin-right:5px;}
        &:first-child{margin-left:10px;}
      }
    }
  }
  // 두산인의 하루
  .today{ 
    .sort-swipe-cont{padding:50px 15px;}
    .ds_profile{
      .info{padding:35px 0;margin:0;background:#fff;
        .h4-title{font-weight:$fw-b;color:$fc-base;margin-bottom:10px;
          &:before{content:'';display:inline-block;width:20px;height:20px;background:url($svg_message)no-repeat center center;margin:0 9px 2px 0;vertical-align:middle;}
        }      
      }
    }
    .step-wrap{width:100%;margin:50px auto 0 auto;
      ul{margin-top:50px;padding-top:40px;border-top:1px solid #e4e4e4;
        li{margin-bottom:35px;color:#666;
          &:last-child{margin-bottom:0;}
          .s-circle{position:relative;padding-left:23px;font-weight:$fw-b;color:#117ada;margin-bottom:5px;
            &:before{content:'';position:absolute;display:inline-block;width:8px;height:8px;background:#117ada;border-radius:50%;top:8px;left:3px;}
            &:after{content:'';position:absolute;display:inline-block;width:14px;height:14px;background:#e5f3fa;border-radius:50%;top:5px;left:0;z-index:-1;}
          }
        }
      }
    }
  }
  .story-wrap{
    &.schedule{
      .schedule-cont{
        .img-wrap{
          img{width:100%;}
        }
      } 
      .plan{width:calc(100% - 40%);left:50%;top:50%;transform:translateX(-100%) translateY(-50%);
        &.ani{transform:translateX(-50%) translateY(-50%);}
        .s-circle{font-size:24px;}
        .h4-title{font-size:16px;}
      } 
    }
  }
  .ds_profile{padding:0 15px;
    .img{min-width:100%;width:100%;height:auto;padding-right:20px;
      &:before{top:25px;left:35px;width:calc(100% - 35px);}
    }
    .info{margin-top:60px;line-height:23px;
      &:before{display:none;}
      .people-head{margin-bottom:20px;
        .sort-company{font-size:15px;}
        .inner{margin-top:7px;
          strong{font-size:25px;}
          .sort-part{margin:3px 0 0 15px;font-size:16px;}
        }
      } 
    }
  }
  .step-by-view{margin:70px 0 0;
    ol{flex-direction:column;align-items:center;width:100%;max-width:300px;height:auto;padding:80px 10px 40px;
      li{width:100%;min-height:150px;
        a{
          &::before{width:110px;height:110px;}
          &.active{
            span{
              &.step01{
                &::before{background-position:-40px 0;}
              }
               &.step02{
                &::before{background-position:-40px -42px;}
               }
               &.step03{
                &::before{background-position:-40px -84px;}
               }
               &.step04{
                &::before{background-position:-40px -122px;}
               }
               &.step05{
                &::before{background-position:-40px -162px;}
               }
            }
          }
        }
        span{padding:45px 0 0 160px;font-size:18px;
          &::before{left:55px;top:50%;width:40px;height:40px;background-image:url($imgpath + 'content/mo_ico_join_step.png');background-size:80px 200px;}
          &::after{left:47px; right:inherit; top:inherit; bottom:-60px; margin-right:0; transform:translateY(0) rotate(45deg); width:14px; height:14px;}
          &.step01{
            &::before{background-position:0 0;}
          }
          &.step02{
            &::before{background-position:0 -42px;}
          }
          &.step03{
            &::before{background-position:0 -84px;}
          }
          &.step04{
            &::before{background-position:0 -122px;}
          }
          &.step05{
            &::before{background-position:0 -162px;}
          }
        }
      }
    }
  }
  .step-wrap{margin:75px auto 0 auto;
    ol{
      li{padding-bottom:60px;
        span{font-size:20px;padding-left:37px;letter-spacing:0;
          &::before{top:0;left:0;font-size:30px;letter-spacing:-1.5px;}
        }
        &:nth-child(1){padding-bottom:60px;
          .img{padding:0 20px 0 15px;}
          .txt{margin-top:60px !important;}
          .bgType{
            &::before{left:60px;bottom:-30px;}
          }
        }
        &:nth-child(2){flex-direction:column-reverse;
          .img{padding-right:75px;}
        }
        &:nth-child(3){
          .img{padding-left:30px;}
        }
        &:nth-child(4){flex-direction:column-reverse;margin-bottom:0;
          .img{padding:0 60px 0 15px;}
          .txt{margin-top:60px !important;}
          .bgType{
            &::before{left:-100px;bottom:-30px;}
          }
        }
        &:nth-child(5){
          .img{padding:0 0 0 40px;}
        }
        .txt{line-height:22px;margin:30px 15px 0 15px !important;
           p{margin-top:15px;font-size:15px;}
        }
      }
    }
  }
}

@keyframes highlights {
  50% { border:1px dashed #117ada;}
  80% { border:1px dashed #fff;}
  100% { border:1px dashed #117ada;}
}

@include mediaQuery(phone_under){
  .sort_swipe_wrap{
    .control{
      button{background:rgba(0, 0, 0, 0.15)}
    }
  }
  .story-wrap{
    &.schedule{
      .plan{width:65%;
        .s-circle{font-size:24px;}
        .h4-title{font-size:16px;}
      }
    }
  }
}