﻿@charset "utf-8";

/**
* base.form
* ------------------
*/

$form-fz : 16px;

$form-check-size : 24px; 
$form-check-big-size : 30px; 
$form-file-height : calc(2.875rem + 2px);
$form-mo-file-height : calc(2.375rem + 2px);

//common
input:not([type="checkbox"]):not([type="radio"]),
select,
textarea,
.form-control{
	@extend .form-control;
	width:100%;
	padding: 11px 18px 11px;
	font-size: 16px;
	border: 1px solid #ccc;
	border-radius: 0;

	//width
	min-width:0;
	// min-width:260px;
	// &.width-sm {min-width: 200px;}
}

html.pc-only{
	input:not([type="checkbox"]):not([type="radio"]),
	textarea,
	.form-control{
		min-width:260px;
	}
	select {
		min-width:260px;
		&.width-sm {min-width: 200px;}
	}
	.input-sch-group.date.search {
		button.btn.btn-srch.btn-icon.btn-readonly {background: #e9ecef;}
		.form-control.search-input {min-width: 182px;}
	}
	.input-sch-group.date.inp-type.search {
		.form-control.search-input {min-width: 337px;}
	}
	.table-area.table-pc.type-wd {
		th {padding-top: 25px;}
		table tbody td.radio-align {vertical-align: middle;}
		input:not([type="checkbox"]):not([type="radio"]),
		textarea,
		.form-control{
			width: 260px;
			min-width:260px;	
		}
		select {
			min-width:260px;
			&.width-sm {min-width: 200px;}
		}

	}
}



.form-number{position: relative;
	@include clearfix;
	input, select{min-width:0 !important;}

	.form-select{float: left;position: relative; width: 40%;
		input, 
		select{width: calc(100% - 16px) !important;}
		&:after{content:'-'; position: absolute;top:0; right: 0px; width: 16px; display: inline-block; text-align: center;line-height:48px; vertical-align: center;}
	}
	.form-group{position: relative;float: left;width: 60%;
		.num-domestic{@include clearfix;
			input{float: left;width:calc(50% - 8px) !important;}
			input:last-child{float: right;}
			&:after{content:'-'; position: absolute;top:0; left: 50%;width: 16px;  margin-left: -8px;display: inline-block; text-align: center;line-height:48px; vertical-align: center;}
		}
		.num-overseas{display:none;
			input{width:100% !important;}
		}
	}
}

//---------------
//Select
select, select.form-control:not([size]):not([multiple]){height: 48px;  }
select {
	&,
	&.form-control {padding-right: 40px;-moz-appearance: none; -webkit-appearance: none; color:#999; font-weight: $fw-l;
		background:#fff url($svg_ico_arrow) top 50% right 16px no-repeat; background-size:16px 10px;}
	&::-ms-expand {display:none; display:hidden;} 
	&:focus {border: 1px solid #666;  color: #333;}
	&:disabled {border: 1px solid #ddd; background: #fafafa;color: #999;}
}



//---------------
//Radio 
.form-check{padding-left: 0; }
.form-check-inline{align-items : baseline;}

//Radio
input[type="radio"]{
	$color-disabled : #999;
	z-index: -1;position: absolute; left: 0;width: 1px; height: 1px; padding: 0; margin: -1px; overflow: hidden; clip:rect(0,0,0,0); border: 0;pointer-events: none;
	&:first-child + label {margin-left: 0;}
	+ label {display: inline-block; position: relative; padding-left: 34px; cursor: pointer; min-height:$form-check-size;
		&:before {content: ''; position: absolute; left: 0; top: 2px; width: $form-check-size; height: $form-check-size; text-align: center; background: #fff; border: 1px solid $color-disabled; border-radius: 50%; /*box-shadow: 0px 1px 2px rgba(0,0,0,0.05), inset 0px -15px 10px -12px rgba(0,0,0,0.05);*/}
	}
	&:checked {
		// + label:active:before {content: ''; display: inline-block; background:  $point-color-primary; border-color: $point-color-primary;}
		+ label:before {content: ''; display: inline-block; background: $point-color-primary; border-color: $point-color-primary;}
		+ label:after {content: ''; display: inline-block; position: absolute; top: 9px; left: 7px; width: 10px; height: 10px; background: #fff; border-radius: 100%;}
	}
	
	&:disabled ~ label::before{background-color:#e9ecef; border: 1px solid $color-disabled;}
	&:disabled ~ label::after{border: 1px solid $color-disabled;}
	&:disabled ~ label{color:#afb5b9}
	&:disabled:checked ~ label::before{background-color:$color-disabled;border: 1px solid $color-disabled;}
	&:disabled:checked ~ label::after{border: 1px solid $color-disabled;}
}



//---------------
//CheckBox
input[type="checkbox"]{
	$svg_form01 : "data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' width='11px' height='7px'%3E%3Cpath fill-rule='evenodd' fill='rgb(255, 255, 255)' d='M1.878,2.464 L4.535,5.121 C4.926,5.511 4.926,6.145 4.535,6.535 C4.145,6.926 3.512,6.926 3.121,6.535 L0.464,3.878 C0.073,3.488 0.073,2.855 0.464,2.464 C0.855,2.074 1.488,2.074 1.878,2.464 Z'/%3E%3Cpath fill-rule='evenodd' fill='rgb(255, 255, 255)' d='M9.710,1.767 L4.799,6.678 C4.391,7.086 3.730,7.086 3.322,6.678 C2.914,6.270 2.914,5.608 3.322,5.200 L8.232,0.289 C8.641,-0.119 9.302,-0.119 9.710,0.289 C10.118,0.697 10.118,1.359 9.710,1.767 Z'/%3E%3C/svg%3E";
	$color-disabled : #999;
	@extend .custom-control-input;

	&:checked ~ label::after{background-image: url($svg_form01); border: 1px solid $point-color-primary;}
	&:disabled ~ label{color:#afb5b9}
	&:disabled:checked ~ label::before{background-color:$color-disabled;border-color:$color-disabled}
	&:disabled:checked ~ label::after{border: 1px solid $color-disabled;}

	& + label{
		@extend .custom-control-label;
		min-height:$form-check-size; padding-left:34px; margin-bottom: .5rem;
		&:before{width:$form-check-size;height:$form-check-size;left:0;top:1px;background: transparent; border: 1px solid $color-disabled;}
		&:after{width:$form-check-size;height:$form-check-size;left:0;top:1px;background-size:16px 12px; border: 1px solid $color-disabled;}
		&.chk-lab::before {top: 8px;}
		&.chk-lab::after {top: 8px;}
	}
	&.wl + label { // title + Checkbox
		min-height:$form-check-big-size; padding-left:40px;
		&:before{width:$form-check-big-size;height:$form-check-big-size;}
		&:after{width:$form-check-big-size;height:$form-check-big-size;}
	}
}

