// 돋보기 인풋박스
.input-sch-group.date.search {
	.btn.btn-srch {position: absolute; top: 0; right: 0; border: 1px solid #ced4da; border-left: 0;}
	.search-input{border-right-color: #fff; }
	.input-group-text{display: flex; align-items: center; padding: 0.375rem 0.75rem; margin-bottom: 0; font-size: 1rem; font-weight: 500; line-height: 1.5; color: #495057; text-align: center; white-space: nowrap; background-color: #fff; border: 1px solid #ccc; border-left-color:#fff; border-radius: 0; }
