// 탈퇴 내용 박스
.outbox-cont {margin-top: 30px; border: 1px solid #e4e4e4; padding: 30px 30px;
	strong {display: block; margin-bottom: 20px; color: #117ada; font-size: 16px;}
	p {font-size: 16px; font-weight: 700;}
}

// 파일찾기
.form-group {
	&.upload {
    .custom-file {
      height: 48px; 
      .custom-file-input {width:280px; height: 48px;}
      .custom-file-label {
        &:after {display: none;}
        &:before {
          content: ''; 
          display: block; 
          border: 1px solid #ccc; 
          position: absolute; 
          top: 0; 
          left: 0; 
          width: calc((100% - 135px) - 10px); 
          height: 48px;
        }
        width: 425px;
        height: 48px; 
        padding: 9px 0 9px 42px; 
        border: 0;
        .ic.srch {
          &:before {    
            left: 20px;
            top: 15px;
          }
          position: absolute;
          top: 0;
          right: 0;
          min-width: 133px;
          height: 48px;
          padding: 9px 0 9px 42px;
		  font-style: normal;
		  font-size: 18px;
          font-weight: $fw-n;
          background: #fff;
          border: 1px solid #999;
        }
      }
    }
	}
	
	@include mediaQuery(pc_under) {
		&.upload {
			.custom-file {
				.custom-file-label { width: auto; 
					&:before { width: calc((100% - 111px) - 32px); }
				}
			}
		}
	}
}

.table-area.table-pc.type-wd {
	table tbody td textarea.form-control.box-type {height: 270px; min-height: 270px;}
}

// 2칼럼 테이블
.table-area.column-type {padding-top: 16px; margin-top: 20px; border-top: 2px solid #999999; border-bottom: 1px solid #e4e4e4;
	th, td {border-bottom: 0; padding-top: 0; padding-bottom: 9px;}
	th {padding-top: 10px;}
	td {padding-right: 30px;}
	+ .table-area.column-type {margin-top: 0; border-top: 1px solid #999999;}
	.form-aa .form-group .input-group.date.monthpicker input {width: 100px; min-width: 100px;}
}

html.pc-only .table-area.column-type td.inline-type {width: 446px;}
html.pc-only .table-area.column-type .form-inline.inline-wd {display: inline-block;}
html.pc-only .table-area.column-type .form-inline.inline-wd .form-group {float: left;}
html.pc-only .table-area.column-type .form-inline.inline-wd:after {content: ''; display: block; clear: both;}
html.pc-only .table-area.column-type .form-inline.inline-wd .form-group .input-group.date.monthpicker input {width: 160px; min-width: 160px;}

