$svg_paging_next :"data:image/svg+xml,%3Csvg xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' preserveAspectRatio='xMidYMid' width='10' height='16' viewBox='0 0 10 16'%3E%3Cdefs%3E%3Cstyle%3E .cls-1 %7B fill: %23666; fill-rule: evenodd; %7D %3C/style%3E%3C/defs%3E%3Cpath d='M10.002,8.012 L1.437,16.009 L0.009,14.676 L7.147,8.011 L0.009,1.347 L1.437,0.014 L8.574,6.678 L8.574,6.678 L10.002,8.012 Z' class='cls-1'/%3E%3C/svg%3E%0A";


.paging-wrap {position:relative; padding-top:0; 
	.pagination {margin-top:30px;}
	.btn-area {position:absolute; right:0; top:-20px;
		@include mediaQuery(pc_under) { position:relative; top:0; }
	}
}

.pagination{margin-top: 20px; margin-bottom: 20px;
	.page-item{margin:0 10px; }
	.page-link{position: relative; display: block; font-size: 16px; line-height: 28px; color: #333; background-color: #f4f4f4; border: 1px solid #f4f4f4; border-radius: 0; padding: 0px 10px; min-width: 30px; text-align: center; }
	.page-btn{position: relative; width: 30px; height: 30px; 
		.page-link{display: block; background:none !important; border: none; border-radius:0;
			span{display: inline-block; line-height: 30px; text-align: center; background: url($svg_paging_prev) center center no-repeat; width: 10px; height: 16px; background-size: 10px 16px; 
				&.ic-next{background: url($svg_paging_next) center center no-repeat;}
			}
		}
	}
}

// 03.08 : reversion
.navigation {display:flex; justify-content: center; margin-top: 20px; margin-bottom: 20px;
	>.page-list-item{position: relative; display: inline-block; font-size: 16px; line-height: 28px; color: #333; margin:0 10px; background-color: #f4f4f4; border: 1px solid #f4f4f4; border-radius: 0; padding: 0px 10px; min-width: 30px; text-align: center; 
		&.page-btn{display: block; width:30px; height:30px; background:none !important; border: none; 
			span{display: inline-block; line-height: 30px; text-align: center; background: url($svg_paging_prev) center center no-repeat; width: 10px; height: 16px; background-size: 10px 16px; 
				&.ic-next{background: url($svg_paging_next) center center no-repeat;}
			}
		}
		&.active{
			z-index: 1; 
			color: $pagination-active-color;
			background-color: $pagination-active-bg; 
			border-color: $pagination-active-border-color;
		}
		&.disabled{ 
			color: $pagination-disabled-color;
			pointer-events: none;
			// Opinionated: remove the "hand" cursor set previously for .page-link
			cursor: auto;
			background-color: $pagination-disabled-bg;
			border-color: $pagination-disabled-border-color;
		}
	}
}