@charset "utf-8";

/*
* component.button
* --------------
*/

// $btn-default-color:                 #fff !default;
$btn-default-bg:                    #fff;
$btn-default-border-color:              #999 ;

// $btn-primary-bg:                 					#117ada;
// $btn-primary-border-color:             #117ada;

$btn-paddingX :20px;

$btn-white-color :#fff;
$btn-gray-color :#ccc;

$btn-margin-bottom : 5px;


.btn.noeffect{
	&,
	&:focus,
	&:hover {border:none; background-color: none; box-shadow: none; }
}

.btn{
	position: relative;
	margin: $btn-margin-bottom 0;
	font-size: 16px; //
	font-weight: $fw-n;
	text-transform: none;
	box-shadow: none;

	/*&.btn-link,
	&.btn-link:hover,
	&.btn-link:focus,
	&.btn-link:active {
		text-decoration: underline !important; padding: 0;
	}*/


	//------
	// Size
	// button-size($padding-y, $padding-x, $font-size, $line-height, $border-radius)
	&.btn-xg,
	.btn-group-xg & {
		@include button-size(24px, $btn-paddingX, 20px, 20px, 0);
		font-weight:bold;
		min-width:200px;
	}

	&.btn-lg,
	.btn-group-lg & {
		@include button-size(19px, $btn-paddingX, 18px, 20px, 0);
		font-weight:bold;
		// min-width:180px;
		/*@include mediaQuery(phone){
			@include button-size(18px, 5px, 16px, 18px, 0);
		}*/
	}

	//default : :h48, fz18
	&{
		@include button-size(13px, $btn-paddingX, 16px, 20px, 0);
		font-weight:normal;
		// min-width:240px;
	}

	&.btn-sm,
	.btn-group-sm & {
		@include button-size(10px, $btn-paddingX, 16px, 16px, 0);
		font-weight:normal;
		min-width:80px;
	}

	&.btn-xs,
	.btn-group-xs & {
		@include button-size(6px, $btn-paddingX, 14px, 14px, 0);
		font-weight:normal;
		min-width:80px;
	}

	&.btn-color {
		color: #333;
		border: 1px solid #999;
	}

	&.btn-gray{
		color:#fff;
		background-color:#666;
		border-color:#666;
	}

	//------
	//btn-round
	&.btn-round {
		position: relative;
		border-radius: 50px;
		@extend .btn-outline;
		&.btn-point-outline { color:$point-color-primary; border-color: $point-color-primary !important; }
		&.btn-del { 
			@include button-size(7px, 16px, 16px, 16px, 16px); 
		}
	}
}


//btn-outline
.btn.btn-outline{border:1px solid #999 !important;}
.btn.btn-outline:hover,
.btn.btn-outline:focus{background-color:#fff;border:1px solid #999; box-shadow:0 0 0 0.2rem rgba(200, 200,200, 0.25)}


//width
.btn{min-width: 240px;}
.btn-width-sm{min-width: 160px;}
.btn-width-m{min-width: 180px;}


//size
//------------------------
//btn-area

.modal-footer{
		.btn-area{
			.btn{@include btn-lg(); flex:1;min-width:0;  }
		}
	}


.btn-area{position: relative;display: flex;font-size: 0;  justify-content: center;padding:5px 0;
	.btn + .btn{margin-left: 10px;}

	&.left{justify-content:flex-start;}
	&.right{justify-content:flex-end;}
	&.btn-xs{.btn{@extend .btn-xs;}}
	&.btn-sm{.btn{@extend .btn-sm;}}
	&.btn-lg{.btn{@extend .btn-lg;}}
	&.btn-xg{.btn{@extend .btn-xg;}}

	&.in {margin-top:20px; padding:0; //사용안함
		.btn {@extend .btn; margin:0; }
	}
	&.last {margin-top:15px; padding:0; justify-content:flex-end;
		@include mediaQuery(pc_under){/*margin-top: 40px;*/margin-bottom: 20px;width: 100%; }
		.btn {@extend .btn; @include btn-lg(); margin-bottom:0; }
		
		//  중간에 버튼 있을 경우 :: 나의 관심직무
		& + .list-wrap {margin-top:50px;
			@include mediaQuery(tablet_under) {
				margin-top:25px;
			}
		}
	}
	&.mt20 {margin-top:20px; padding:0;
		.btn {margin:0;}
	}

	//left1 , right2
	&.btn-between{ padding: 0;
		justify-content: space-between;
		.btn{flex:none;
			// @include btn-lg();
			padding: 19px 20px;
			font-size: 18px;
			margin-bottom: 0;
		}
		.btn:nth-of-type(1){margin-right:auto;}
		.btn:nth-of-type(2){margin-left:auto;}

		@include mediaQuery(pc_under){
			.btn{flex:1;min-width: 0;}
			.btn:nth-of-type(1){margin-right:0px;}
			.btn:nth-of-type(2){margin-left:10px;}
		}
	}

	//tablet, phone 100%
	@include mediaQuery(pc_under){
		.btn{flex:1;min-width: 0 !important;}
	}
}


@include mediaQuery(tablet_under){
	.btn{padding-left: 0;padding-right: 0;}
}

//radioBtn, checkBtn
.btn-group{
	.btn{min-width: 0;}
	// @extend .btn-area;
	.btn:not(:disabled):not(.disabled):active, .btn:not(:disabled):not(.disabled).active{
		background: $point-color-primary; color: #fff;
		border: 1px solid $point-color-primary !important;
	}
}




//Plus BTN
.btn-plus{
	// @extend .btn-width-sm;
	.ic.plus{display: inline-block; margin-right: 8px; line-height: 27px; width: 12px; height:16px; background:url($svg_bullet_plus) center center no-repeat;}
	.ic.plus2{display: inline-block; margin-right: 8px; line-height: 27px; vertical-align:bottom; width:16px; height:18px; background:url($svg_doc) center center no-repeat;}
	.ic.plus3{display: inline-block; margin-right: 8px; line-height: 27px; vertical-align:bottom; width:16px; height:18px; background:url($svg_ico_plus_b) center center no-repeat;}
	.ic.pen{display: inline-block; margin-right: 8px; line-height: 27px; vertical-align:bottom; width:19px; height:19px; background:url($svg_ico_pen) center center no-repeat;}
}

//DBS재응시 | 2회
.btn{
	span.bar{position: relative; padding-right: 8px; margin-right: 8px;}
	span.bar:after{content:''; position: absolute; top: 6px; right: 0; width: 1px; height: 16px; background-color: $point-color-primary;}
}

//Search BTN :: 텍스트 있을 경우 & 없을 경우
.btn-srch{ min-width:auto;
	.ic.srch {position:relative; padding-left:26px;
		&:before {content:''; display:inline-block; position:absolute; left:0; top:4px; width:20px; height:20px; background:url($svg_ico_srch) center center no-repeat;
			@include mediaQuery(pc_under) {
				width:16px;
				height:15px;
				top:7px;
				background:url($svg_mo_ico_srch) center center no-repeat;
				background-size:16px 15px;
			}
		}
		&.white:before{background:url($svg_ico_srch_white) center center no-repeat;
			@include mediaQuery(pc_under) {
				// background:url($svg_mo_ico_srch_white) center center no-repeat;
				background-size:16px 15px;
			}
		}
	}
	&.btn-icon{padding:13px 9px;}
}

.btn-people{
	.ic.people{display:inline-block; margin-right:8px; line-height:27px; vertical-align:bottom; width:18px; height:18px; background:url($svg_people) center center no-repeat;}
}
.btn-doc{
	.ic.doc{display:inline-block; margin-right:8px; line-height:27px; vertical-align:bottom; width:16px; height:18px; background:url($svg_doc) center center no-repeat;}
}

//Call BTN
.btn-call {padding:13px 20px; margin:0; font-size: $fz-md; font-weight:700;
	.ic.call {position:relative; padding-left:26px;
		&:before {content:''; display:inline-block; position:absolute; left:0; top:6px; width:18px; height:18px; background:url($svg_ico_call) center center no-repeat;
			@include mediaQuery(pc_under) {

			}
		}
	}
}

//Keyword delete BTN
.btn-del {min-width:80px;
	.ico {position:relative; display:inline-block; width:12px; height:12px; margin-left:8px; font-style:normal; 
		&:before, &:after {content:''; position:absolute; top:5px; display:inline-block; width:14px; height:2px; background: $point-color-primary;}
		&:before {left:0; transform:rotate(45deg); }
		&:after {left:0; transform:rotate(-45deg); }
	}
}

//------------------------
//btn-fixed
.btn-fix{bottom:-160px; margin-top: 20px; 
	.btn-area{margin: 0 auto; width:100%; }
}

body.ui-btn-fix{
	//버튼 fixed 될때 없어지는 높이로 인한 떨림 제제 (content padding-bottom(100) + btnH(60))
	.content{padding-bottom: 160px;} 
	.btn-fix{z-index:300; position: fixed;bottom: 0;left: 0; right: 0;width:100%; margin-top: 0;padding:14px 0 20px;border-top: 1px solid #ccc;background-color: #fff;justify-content:center;
		.btn-area{padding:0 40px;
			@include mediaQuery(pc_upper2){
				padding: 0; width: $layout-content-width;
			}
			@include mediaQuery(tablet_under){
				padding: 0 15px;
			}
		}
		@include shadow-big();
		@include common-transition(bottom);
	}
}