// $font-path-noto : '/assets/font/notosans/';

@font-face {
	font-family: 'Noto Sans';
	font-style: normal;
	font-weight: 100;
	src: url($font-path-noto + 'NotoSansKR-Thin.woff2') format('woff2'),
			 url($font-path-noto + 'NotoSansKR-Thin.woff') format('woff');
}
