@charset "utf-8";

/**
* base.magic.mo
* ------------------
* 모바일에서만 적용
*/

$mobile-prefix :'.mo-';

@include mediaQuery(phone){
	//------------------------------------
	// Magic Class
	//------------------------------------
	// https://docs.emmet.io/cheat-sheet/

		#{$mobile-prefix}clearfix {*zoom:1;
			&:before, &:after{display:block; clear:both; content:"";}
		}
		#{$mobile-prefix}blind{position:absolute;display: inline-block;font-size: 1px;width: 1px;height: 1px;padding: 0;margin: -1px;text-indent: -9999px;visibility: hidden;overflow: hidden;border: 0;clip: rect(0,0,0,0);}

		//Visual Formatting
		#{$mobile-prefix}pos-r {position:relative !important;}
		#{$mobile-prefix}pos-a {position:absolute !important;}
		#{$mobile-prefix}pos-f {position:fixed !important;}
		#{$mobile-prefix}pos-s {position:static !important;}

		#{$mobile-prefix}fl-l, #{$mobile-prefix}fl {float:left !important}
		#{$mobile-prefix}fl-r, #{$mobile-prefix}fr {float:right !important}
		#{$mobile-prefix}fl-n, #{$mobile-prefix}fn {float:none !important;}

		#{$mobile-prefix}cl{clear:both !important;}
		#{$mobile-prefix}cl-n{clear:none !important;}
		#{$mobile-prefix}cl-l{clear:left !important;}
		#{$mobile-prefix}cl-r{clear:right !important;}
		#{$mobile-prefix}cl-b{clear:both !important;}

		#{$mobile-prefix}d-b, #{$mobile-prefix}db {display:block !important;}
		#{$mobile-prefix}d-n{display:none !important;}
		#{$mobile-prefix}d-f{display:flex !important;}
		#{$mobile-prefix}d-if{display:inline-flex !important;}
		#{$mobile-prefix}d-i{display:inline !important;}
		#{$mobile-prefix}d-ib{display:inline-block !important;}
		#{$mobile-prefix}d-li{display:list-item !important;}
		#{$mobile-prefix}d-tb{display:table !important;}
		#{$mobile-prefix}d-itb{display:inline-table !important;}
		#{$mobile-prefix}d-tbcl{display:table-column !important;}
		#{$mobile-prefix}d-tbr{display:table-row !important;}
		#{$mobile-prefix}d-tbc{display:table-cell !important;}

		#{$mobile-prefix}v-v{visibility:visible !important;}
		#{$mobile-prefix}v-h{visibility:hidden !important;}
		#{$mobile-prefix}v-c{visibility:collapse !important;}

		#{$mobile-prefix}ov-v{overflow:visible !important;}
		#{$mobile-prefix}ov-h{overflow:hidden !important;}
		#{$mobile-prefix}ov-s{overflow:scroll !important;}
		#{$mobile-prefix}ov-a{overflow:auto !important;}
		#{$mobile-prefix}ovx-s{overflow-x:scroll !important;}
		#{$mobile-prefix}ovx-a{overflow-x:auto !important;}
		#{$mobile-prefix}ovy-s{overflow-y:scroll !important;}
		#{$mobile-prefix}ovy-a{overflow-y:auto !important;}

		#{$mobile-prefix}cur-a{cursor:auto !important;}
		#{$mobile-prefix}cur-d{cursor:default !important;}
		#{$mobile-prefix}cur-ha{cursor:hand !important;}
		#{$mobile-prefix}cur-hl{cursor:help !important;}
		#{$mobile-prefix}cur-m{cursor:move !important;}
		#{$mobile-prefix}cur-p{cursor:pointer !important;}

		//Box Sizing
		#{$mobile-prefix}bx, #{$mobile-prefix}bx-b{box-sizing:border-box !important;}
		#{$mobile-prefix}bx-c{box-sizing:content-box !important;}

		#{$mobile-prefix}bxs-n{box-shadow:none !important;}

		#{$mobile-prefix}w-a{width:auto !important;}
		#{$mobile-prefix}h-a{height:auto !important;}

		//Font
		#{$mobile-prefix}fw-n{font-weight:normal !important;}
		#{$mobile-prefix}fw-b{font-weight:bold !important;}

		#{$mobile-prefix}fs-n{font-style:normal !important;}
		#{$mobile-prefix}fs-i{font-style:italic !important;}

		#{$mobile-prefix}fv-n{font-variant:normal !important;}
		#{$mobile-prefix}fv-sc{font-variant:small-caps !important;}

		#{$mobile-prefix}ff-s{font-family:serif;}
		#{$mobile-prefix}ff-ss{font-family:sans-serif;}
		#{$mobile-prefix}ff-m{font-family:monospace;}
		#{$mobile-prefix}ff-a{font-family:Arial, "Helvetica Neue", Helvetica, sans-serif;}

		//Text
		#{$mobile-prefix}va-t{vertical-align:top !important;}
		#{$mobile-prefix}va-sup{vertical-align:super !important;}
		#{$mobile-prefix}va-tt{vertical-align:text-top !important;}
		#{$mobile-prefix}va-m{vertical-align:middle !important;}
		#{$mobile-prefix}va-bl{vertical-align:baseline !important;}
		#{$mobile-prefix}va-b{vertical-align:bottom !important;}
		#{$mobile-prefix}va-tb{vertical-align:text-bottom !important;}
		#{$mobile-prefix}va-sub{vertical-align:sub !important;}

		#{$mobile-prefix}ta-l{text-align:left !important;}
		#{$mobile-prefix}ta-c{text-align:center !important;}
		#{$mobile-prefix}ta-r{text-align:right !important;}
		#{$mobile-prefix}ta-j{text-align:justify !important;}

		#{$mobile-prefix}tov, #{$mobile-prefix}tov-e{text-overflow:ellipsis !important;}

		#{$mobile-prefix}td-n{text-decoration:none !important;}
		#{$mobile-prefix}td-u{text-decoration:underline !important;}
		#{$mobile-prefix}td-o{text-decoration:overline !important;}
		#{$mobile-prefix}td-l{text-decoration:line-through !important;}

		#{$mobile-prefix}ti-{text-indent:-9999px !important;}

		#{$mobile-prefix}whs-n{white-space:normal !important;}
		#{$mobile-prefix}whs-nw{white-space:nowrap !important;}
		#{$mobile-prefix}whs-p{white-space:pre !important;}

		#{$mobile-prefix}wob-n{word-break:normal !important;}
		#{$mobile-prefix}wob-k{word-break:keep-all !important;}
		#{$mobile-prefix}wob-b{word-break:break-all !important;}

		#{$mobile-prefix}wow-nm{word-wrap:normal !important;}
		#{$mobile-prefix}wow-n{word-wrap:none !important;}
		#{$mobile-prefix}wow-b{word-wrap:break-word !important;}

		//Letter-spacing
		#{$mobile-prefix}ls1{letter-spacing:1px}
		#{$mobile-prefix}ls2{letter-spacing:2px}
		#{$mobile-prefix}ls-1{letter-spacing:-1px}
		#{$mobile-prefix}ls-2{letter-spacing:-2px}

		//line-height
		#{$mobile-prefix}lh38{line-height:38px!important}
		#{$mobile-prefix}lh40{line-height:40px!important}

		//Background
		#{$mobile-prefix}bg-n{background:none !important;}

		#{$mobile-prefix}bgz-a{background-size:auto !important;}
		#{$mobile-prefix}bgz-ct{background-size:contain !important;}
		#{$mobile-prefix}bgz-co{background-size:cover !important;}

		//Color

		//Table
		#{$mobile-prefix}tbl-a{table-layout:auto !important;}
		#{$mobile-prefix}btn-f{table-layout:fixed !important;}

		//Border
		#{$mobile-prefix}bd-n{border:none !important;}
		#{$mobile-prefix}bdt-n{border-top:none !important;}
		#{$mobile-prefix}bdb-n{border-bottom:none !important;}
		#{$mobile-prefix}bdl-n{border-left:none !important;}
		#{$mobile-prefix}bdr-n{border-right:none !important;}

		//Others
		#{$mobile-prefix}fxd-c{flex-direction:column !important;}
		#{$mobile-prefix}fxd-cr{flex-direction:column-reverse !important;}
		#{$mobile-prefix}fxd-r{flex-direction:row !important;}
		#{$mobile-prefix}fxd-rr{flex-direction:row-reverse !important;}

		#{$mobile-prefix}fxw-n{flex-wrap:nowrap !important;}
		#{$mobile-prefix}fxw-w{flex-wrap:wrap !important;}
		#{$mobile-prefix}fxw-wr{flex-wrap:wrap-reverse !important;}

		#{$mobile-prefix}jc-c{justify-content:center !important;}
		#{$mobile-prefix}lc-fe{justify-content:flex-end !important;}
		#{$mobile-prefix}jc-fs{justify-content:flex-start !important;}
		#{$mobile-prefix}jc-sa{justify-content:space-around !important;}
		#{$mobile-prefix}jc-sb{justify-content:space-between !important;}

		#{$mobile-prefix}op-0{opacity:0 !important;}
		#{$mobile-prefix}op-30{opacity:.3 !important;}
		#{$mobile-prefix}op-50{opacity:.5 !important;}
		#{$mobile-prefix}op-80{opacity:.8 !important;}
		#{$mobile-prefix}op-100{opacity:1 !important;}

		#{$mobile-prefix}op-ie{filter:progid:DXImageTransform.Microsoft.Alpha(Opacity=100);}
		#{$mobile-prefix}op-ms{-ms-filter:'progid:DXImageTransform.Microsoft.Alpha(Opacity=100)';}


//-----------------------------------
//Box Sizing
//-----------------------------------

		// fontsize :10	~ 40 , 2++ 
		@for $c from 5 through 20{#{$mobile-prefix}fz#{$c *2} {font-size:$c*2px !important} }

		// margin : -50 ~ 100 , 5++ == 
		@for $c from 0 through 10{#{$mobile-prefix}m#{$c *5} {margin:$c*5px !important} }//mo_m00
		@for $c from -10 through 20{#{$mobile-prefix}ml#{$c *5} {margin-left:$c*5px !important} }
		@for $c from -10 through 20{#{$mobile-prefix}mt#{$c *5} {margin-top:$c*5px !important} }
		@for $c from -10 through 20{#{$mobile-prefix}mr#{$c *5} {margin-right:$c*5px !important} }
		@for $c from -10 through 20{#{$mobile-prefix}mb#{$c *5} {margin-bottom:$c*5px !important} }

		// padding :  -50 ~ 100 , 5++ ==  
		@for $c from 0 through 10{#{$mobile-prefix}p#{$c *5} {padding:$c*5px !important} }//mo-p00
		@for $c from 0 through 20{#{$mobile-prefix}pl#{$c *5} {padding-left:$c*5px !important} }
		@for $c from 0 through 20{#{$mobile-prefix}pt#{$c *5} {padding-top:$c*5px !important} }
		@for $c from 0 through 20{#{$mobile-prefix}pr#{$c *5} {padding-right:$c*5px !important} }
		@for $c from 0 through 20{#{$mobile-prefix}pb#{$c *5} {padding-bottom:$c*5px !important} }

		/* width == */
		@for $c from 0 through 20{#{$mobile-prefix}w#{$c *5} {width:$c*5px !important} }//w00
		@for $c from 0 through 10{#{$mobile-prefix}w#{$c *100} {width:$c*100px !important} }

}//End of MediaQuery(phone)



